<template>
<div>
  <panel-title :crumbs="crumbs" />
  <!--  -->
  <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30" v-if="this.isEditType == false">
    <div class="mytheme-mg-top-20 title">
      <!---->
      <span></span>
      <!---->
      <div class="fg">
        <Button type="primary" @click="setOrganAboutus"></Button>
      </div>
    </div>
    <div class="content" v-html="this.aboutData"></div>
  </Card>
  <!--  -->
  <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30" v-if="this.isEditType == true">
    <div class="mytheme-mg-top-20 title">
      <!---->
      <span></span>
    </div>
    <!--  -->
    <div style="height: initial;" v-if='isShowEditor'>
      <wangeditor :content='aboutData' :catchData="catchAbout" ></wangeditor>
    </div>
    <div style="text-align: center; margin-top: 20px;">
      <!---->
      <Button type="primary" @click="comfirmAbout"></Button>
      <!---->
      <Button @click="concelAbout"></Button>
    </div>
  </Card>
</div>
</template>
<script>
import { Card, Button } from "iview";
import panelTitle from "@/components/panelTitle";
import Server from "@/service/request";
import Path from "@/service/Path";
import wangeditor from '@/components/wangEditor';
export default {
  name: "AboutUs",
  components: { panelTitle, Card, Button, wangeditor },
  data() {
    return {
      crumbs: [{ name: "", route: "" }],
      aboutData: '', //
      isShowEditor: false, //
      isEditType: false //
    };
  },
  created() {
    this.getOrganAboutus();
  },
  methods: {
    // 
    catchAbout(value){
      this.aboutData = value;      //data
    },
    //
    getOrganAboutus() {
      Server.post(Path.getOrganAboutus,
      {}, res => {
        if(res.code == 0){
          //this.$Message.success(res.info);
          this.aboutData = res.data.aboutus;
        }else {
          this.$Message.error(res.info);
          this.aboutData = '';
        }
      })
    },
    //
    setOrganAboutus() {
      this.isEditType = true;
      Server.post(Path.getOrganAboutus,
      {}, res => {
        if(res.code == 0){
          this.isShowEditor = true;
          //this.$Message.success(res.info);
          this.aboutData = res.data.aboutus;
        }else {
          this.$Message.error(res.info);
          this.aboutData = '';
        }
      })
    },
    //
    comfirmAbout() {
      Server.post(Path.setOrganAboutus,{
        aboutus: this.aboutData
      }, res => {
        if(res.code == 0){
          //this.$Message.success(res.info);
          this.isEditType = false;
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    //
    concelAbout() {
      this.isEditType = false;
    }
  }
};
</script>
<style scoped lang='less'>
@import "~@/assets/less/table.less";
.title{
  color: #666;
  font-size:16px;
  font-family:MicrosoftYaHei-Bold;
  font-weight:bold;
  line-height: 36px;
  overflow: hidden;
  padding-bottom: 10px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 15px;
}
.content{
  min-height: 600px;
}

</style>



