import Vue from "vue";
import Router from "vue-router";
import Server from '@/service/request';
import Path from '@/service/Path';

//-
import webAdminIndex from "@/container/webAdminIndex";
//-
import Main from "@/container/CalendarSchedule";
//--
import MainLessonList from "@/container/CalendarSchedule/LessonList";
//--
import LessonDetailsEdit from "@/container/CalendarSchedule/LessonDetailsEdit";

//-
import ClassTypeManagement from "@/container/ClassTypeManagement";
//-
import TwoClassType from "@/container/ClassTypeManagement/TwoClassType";
//-
import LowerClass from "@/container/ClassTypeManagement/LowerClassType/LowerClass";
//
import Small from "@/container/ArrangingManagement/Small";
// 
import ChooseClassType from "@/container/ArrangingManagement/Edit/ChooseClassType";
import EditLessonView from "@/container/CourseManagement/EditManagement";
//
import AddLesson from "@/container/CourseManagement/AddLessonItem";
import EditLesson from "@/container/CourseManagement/EditManagement/EditLesson";
import EditLessonTime from "@/container/CourseManagement/EditManagement/EditLessonTime/EditTime";
import EditClass from "@/container/CourseManagement/EditManagement/Edit/Edit";
//
import CourseInfo from "@/container/CourseManagement/CourseInfo/CourseInfo"; 
//
import ClassManagement from "@/container/ClassManagement";
//-
import EditClassInfo from "@/container/ClassManagement/EditCLass/EditClassInfo";
import EditCourseTime from "@/container/ClassManagement/EditClass/EditCourseTime";
//-
import classView from "@/container/ClassManagement/classView";

//
import MateriaListPage from "@/container/MaterialManagement/MateriaList";
//
import PersonList from "@/container/MaterialManagement/PersonList";
//
import PersonListView from "@/container/MaterialManagement/PersonListView";
// 
import MaterialList from "@/container/MaterialManagement";
// 
import MaterialInfo from "@/container/MaterialManagement/MaterListInfoEdit/MaterInfo";


//---
import QuestionBank from "@/container/TeachingAffairs/QuestionBank";
//--
import periodList from "@/container/TeachingAffairs/QuestionBank/periodList";
//--/
import editQues from "@/container/TeachingAffairs/QuestionBank/editQues";

//
import homework from "@/container/TeachingAffairs/homework";
//--
import notSH from "@/container/TeachingAffairs/homework/notRead/notSH";
//--
import haveSH from "@/container/TeachingAffairs/homework/haveRead/haveSH";
//--
import homeworkDetail from "@/container/TeachingAffairs/homework/common/homeworkDetail";

//
import KnowledgeConfig from "@/container/TeachingAffairs/KnowledgeConfig";
// --
import ConfigAllImg from "@/container/TeachingAffairs/KnowledgeConfig/ConfigAllImg";

//
import KnowledgeManagement from "@/container/TeachingAffairs/KnowledgeManagement";

//
import AdjustingCurriculumSchedule from "@/container/TeachingAffairs/AdjustingCurriculumSchedule";

//
import ChangeShift from "@/container/TeachingAffairs/ChangeShift";

//
import RewardSet from "@/container/TeachingAffairs/RewardSet";
//
import RewardShowAdd from "@/container/TeachingAffairs/RewardSet/RewardShowAdd";

//
import SignInStatistics from "@/container/TeachingAffairs/SignInStatistics";

//
import SplendidMoment from "@/container/TeachingAffairs/SplendidMoment";
//--
import splendidDetail from "@/container/TeachingAffairs/SplendidMoment/splendidDetail";


//--
import TeacherList from "@/container/UserManagement/TeacherManage";

//--
import StudentManagement from "@/container/UserManagement/StudentManagement";
//--
import StudentInfo from "@/container/UserManagement/StudentManagement/StudentInfo";

//--
import StudentClassify from "@/container/UserManagement/StudentClassify";

//--
import StudentLabel from "@/container/UserManagement/StudentLabel";


//
import EssayManagement from '@/container/EssayManagement';
//--
import EssayDetail from '@/container/EssayManagement/EssayDetail';
//--
import ReaOver from '@/container/EssayManagement/ReaOver';
//----
import EssayList from '@/container/EssayManagement/ReaOver/EssayList';


// 
import AccountDetails from "@/container/OrdersManagement/AccountDetails";
// 
import hasOrder from "@/container/OrdersManagement/AccountDetails/hasOrder";
import waitOrder from "@/container/OrdersManagement/AccountDetails/waitOrder";
// --
import OrderDetails from "@/container/OrdersManagement/OrderDetails";

// 
import MoneyCenter from "@/container/OrdersManagement/MoneyCenter";
// --
import TeacherMoney from "@/container/OrdersManagement/TeacherMoney";


//  
import CourseRecommended from "@/container/CouponsManagement/CourseRecommended";

//  
import TeacherRecommended from "@/container/CouponsManagement/TeacherRecommended";

//  
import AdvertIndexList from "@/container/CouponsManagement/AdvertIndexList";
//  --/
import AdvertIndex from "@/container/CouponsManagement/AdvertIndexList/AdvertIndex";

//  
import FreeLesson from "@/container/CouponsManagement/FreeLesson";

//  
import SetMealList from "@/container/CouponsManagement/SetMealList";
//  --
import AddSetMeal from "@/container/CouponsManagement/SetMealList/AddSetMeal";
//  --
import GetMealList from "@/container/CouponsManagement/SetMealList/GetMealList";
//  --
import AddMealList from "@/container/CouponsManagement/SetMealList/AddMealList";

// 
import EnterpriseSettings from "@/container/Settings/EnterpriseSettings";
// 
import AboutUs from "@/container/Settings/AboutUs";
// 
import DownloadSetting from "@/container/Settings/DownloadSetting";

//
import RightsGroup from "@/container/RightsGroup";
//
import AddGroup from "@/container/RightsGroup/AddGroup";
//
import RightsManagement from "@/container/RightsManagement";
//
import AddMembers from "@/container/RightsManagement/AddMembers";

//import Layout from "../layout/index";
import content from "@/components/content";
import login from "@/container/login";


Vue.use(Router);

export default new Router({
  routes: [
    {
      path:'/',
      component: content,
      redirect: "/webAdminIndex",
      children:[
        //-
        // {
        //   path: "",
        //   component: webAdminIndex
        // },
        //-
        {
          path: "/webAdminIndex",
          component: webAdminIndex,
          name: "webAdminIndex"
        },
        //-
        {
          path: "/home",
          component: Main
        },
        //--
        {
          path: "/MainLessonList",
          name: "MainLessonList",
          component: MainLessonList
        },
        //--
        {
          path: "/LessonDetailsEdit",
          name: "LessonDetailsEdit",
          component: LessonDetailsEdit
        },
        //
        {
          path: "/ClassType",
          component: ClassTypeManagement
        },
        //
        {
          path: "/TwoClassType/:id",
          component: TwoClassType
        },
        //
        {
          path: "/LowerClass/:id",
          component: LowerClass
        },
        //
        {
          path: "/Small",
          component: Small
        },
        
        //
        {
          path: "/CourseInfo/:id/:curriculumid",
          component: CourseInfo
        },
        //  -
        {
          path: "/ChooseClassType",
          component: ChooseClassType,
          name: "ChooseClassType"
        },
        //-
        {
          path: "/AddLesson",
          component: AddLesson,
          name: "AddLesson",
          children: [
            {
              path: "",
              component: EditLesson
            },
            {
              path: "EditLessonTime",
              component: EditLessonTime,
              name: "EditLessonTime"
            },
            {
              path: "Edit",
              component: EditClass,
              name: "EditClass"
            }
          ]
        },
        //
        {
          path: "/EditLesson/:id",
          component: EditLessonView,
          children: [
            {
              path: "",
              component: EditLesson
            },
            {
              path: "EditLessonTime",
              component: EditLessonTime,
              name: "EditLessonTime"
            },
            {
              path: "Edit",
              component: EditClass,
              name: "EditClass"
            }
          ]
        },
        //
        {
          path: "/ClassManagement", 
          component: ClassManagement,
          name: 'ClassManagement'
        },
        // -- 
        {
          path: "/EditClassInfo", 
          component: EditClassInfo,
          name: "EditClassInfo"
        },
        //--
        {
          path: "/EditCourseTime", 
          component: EditCourseTime,
          name: "EditCourseTime"
        },
        //-
        {
          path: "/classView", 
          component: classView
        },
        //
        {
          path: "/MateriaListPage", 
          component: MateriaListPage,
          children: [{
            path: "/MaterialList", //
            component: MaterialList
          },{
            path: "/PersonList", //
            component: PersonList
          }]
        },
        //
        {
          path: "/MaterialInfo/:id", 
          component: MaterialInfo,
          name: "MaterialInfo"
        },
        //
        {
          path: "/PersonListView", 
          component: PersonListView,
          name: "PersonListView"
        },

         //  --
         {
          path: "/QuestionBank",
          component: QuestionBank,
          name: "QuestionBank"
        },
        //  --
        {
          path: "/periodList",
          component: periodList,
          name: "periodList"
        },
        //  --/
        {
          path: "/editQues/:enterType",
          component: editQues,
          name: "editQues"
        },
        //  
        {
          path: "/homework",
          component: homework,
          name: "homework"
        },
        //--
        {
          path: "notSH",
          component: notSH
        },
        //--
        {
          path: "haveSH",
          component: haveSH
        },
        //--
        {
          path: "homeworkDetail",
          component: homeworkDetail
        },
        //  
        {
          path: "/KnowledgeConfig",
          component: KnowledgeConfig,
          name: "KnowledgeConfig"
        },
        //  --
        {
          path: "/ConfigAllImg",
          component: ConfigAllImg,
          name: "ConfigAllImg"
        },
        //  
        {
          path: "/KnowledgeManagement",
          component: KnowledgeManagement,
          name: "KnowledgeManagement"
        },
        //  
        {
          path: "/AdjustingCurriculumSchedule",
          component: AdjustingCurriculumSchedule,
          name: "AdjustingCurriculumSchedule"
        },
        //  
        {
          path: "/ChangeShift",
          component: ChangeShift,
          name: "ChangeShift"
        },
        //  
        {
          path: "/RewardSet",
          component: RewardSet,
          name: "RewardSet"
        },
        //  --
        {
          path: "/RewardShowAdd/:type/:id",
          component: RewardShowAdd,
          name: "RewardShowAdd"
        },
        //  
        {
          path: "/SignInStatistics",
          component: SignInStatistics,
          name: "SignInStatistics"
        },
        //  
        {
          path: "/SplendidMoment",
          component: SplendidMoment,
          name: "SplendidMoment"
        },//  --
        {
          path: "/splendidDetail",
          component: splendidDetail,
          name: "splendidDetail"
        },

        // 
        {
          path: "/TeacherList",
          component: TeacherList,
          name: "TeacherList"
        },
        // 
         {
          path: "/StudentManagement",
          component: StudentManagement,
          name: "StudentManagement"
        },
        // --
        {
          path: "/StudentInfo/:id",
          component: StudentInfo,
          name: "StudentInfo"
        },
        // 
         {
          path: "/StudentClassify",
          component: StudentClassify,
          name: "StudentClassify"
        },
        // 
        {
          path: "/StudentLabel",
          component: StudentLabel,
          name: "StudentLabel"
        },
        // 
        {
          path: "/EssayManagement",
          component: EssayManagement,
          name: "EssayManagement"
        },
        // 
        {
          path: "/EssayDetail",
          component: EssayDetail,
          name: "EssayDetail"
        },
        // 
        {
          path: "/ReaOver",
          component: ReaOver,
          name: "ReaOver"
        },
        // --
        {
          path: "/EssayList",
          component: EssayList,
          name: "EssayList"
        },

         // 
         {
          path: "/AccountDetails",
          component: AccountDetails,
          children:[
            {
              path:'',
              component: hasOrder
            },
            {
              path:'wait',
              component: waitOrder
            },
          ]

        },
        // --
        { path: "/OrderDetails", component: OrderDetails },
        // 
        { path: "/MoneyCenter", component: MoneyCenter },
        // --
        { path: "/TeacherMoney", component: TeacherMoney },

       //  
       {
        path: "/CourseRecommended",
        component: CourseRecommended,
        name: "CourseRecommended"
      },
      //  
      {
        path: "/TeacherRecommended",
        component: TeacherRecommended,
        name: "TeacherRecommended"
      },
       //  
      {
        path: "/AdvertIndexList",
        component: AdvertIndexList,
        name: "AdvertIndexList"
      },
      //  --/
      {
        path: "/AdvertIndex",
        component: AdvertIndex,
        name: "AdvertIndex"
      },
       //  
       {
        path: "/FreeLesson",
        component: FreeLesson,
        name: "FreeLesson"
      },
       //  
      {
        path: "/SetMealList",
        component: SetMealList,
        name: "SetMealList"
      },
       // --
       {
        path: "/AddSetMeal",
        component: AddSetMeal,
        name: "AddSetMeal"
      },
      // --
      {
        path: "/GetMealList",
        component: GetMealList,
        name: "GetMealList"
      },
       // --
      {
        path: "/AddMealList",
        component: AddMealList,
        name: "AddMealList"
      },

      // 
      {
        path: "/EnterpriseSettings",
        component: EnterpriseSettings,
        name: "EnterpriseSettings"
      },
      // 
      {
        path: "/AboutUs",
        component: AboutUs,
        name: "AboutUs"
      },
      //-
      {
        path: "/DownloadSetting",
        component: DownloadSetting,
        name: "/DownloadSetting"
      },

      //
      {
        path: "/RightsGroup",
        component: RightsGroup,
        name: "RightsGroup"
      },
      //
      {
        path: "/AddGroup",
        component: AddGroup,
        name: "AddGroup"
      },
      //
      {
        path: "/RightsManagement",
        component: RightsManagement,
        name: "RightsManagement"
      },
      //
      {
        path: "/AddMembers",
        component: AddMembers,
        name: "AddMembers"
      },
      ]
    },
    {
      path:'/login',
      component: login,
    },
  ]
});



