<template>
  <div id="LowerClassType">
    <panel-title :crumbs="crumbs" />
    <Card dis-hover  class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <template>
        <!--   -->
        <Button type="primary" @click="showTypeEditModal('add')">{{$t('classifyManagepage.Addclassification')}}</Button>
      </template>
      <template>
          <i-table  class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="columns1" :data="data1"></i-table>
      </template>
    </Card>
    
    <Modal
      v-model="LessonTypeEditModel"
      width="580"
      :closable="false">
      <div slot="header">{{LessonTypeText}}</div>
      <div class="type-content">
        <Form :label-width="120" ref='formData' :model="formData" :rules="ruleCustom">
          <Row>
            <Col>
              <!---->
              <FormItem label=":" prop="cateName">
                <Input placeholder=",3" :maxlength="3" v-model.trim="formData.cateName" style=" width: 380px;" @keyup.enter.native="ok('formData')"/>
                <!-- <div class="error hui">3</div> -->
              </FormItem>
            </Col>
            <Col>
              <FormItem label="">
                <Upload
                  :data="uploadid"
                  @success="getHeaderUrl" style="width:32px;">
                  <div class="loadImg">
                    <img class="photo" :src="formData.CateImgUrl" alt="" width="32" height="32" style="cursor:pointer;">
                  </div>
                </Upload>
                <!-- <div class="error hui">156156px</div> -->
              </FormItem>
            </Col>
          </Row>
        </Form>
      </div>
      <div slot="footer">
       <!--  -->
        <Button type="primary"  @click="ok('formData')">{{$t('Common.sure')}}</Button>
        <!--  -->
        <Button @click="reset('formData')">{{$t('Common.cancel')}}</Button>
      </div>
  </Modal>
  <Modal
    v-model="LessonTypeDeleteModel"
    :closable="false">
    <div slot="header"></div>
    <div class="type-content" v-if="this.isCourse == false">
     <div class="input-box">
        <!---->
        {{$t('classifyManagepage.remind1')}}
      </div>
    </div>
    <div class="type-content" v-if="this.isCourse == true">
      <div class="input-box">
        
      </div>
      <i-table :columns="columnscur" :data="datacur"></i-table>
      <div class="input-box" style="overflow: hidden">
        <pagination :total="total" :display="display" :current-page='current' @pagechange="pagechange"  v-if='total>0'></pagination>
      </div>
    </div>
    <div slot="footer" v-if="this.isCourse == false">
      <Button type="primary" @click="okDelete">{{$t('Common.sure')}}</Button>
      <Button @click="LessonTypeDeleteModel = false;">{{$t('Common.cancel')}}</Button>
    </div>
    <div slot="footer" v-if="this.isCourse == true">
      <Button type="warning" @click="LessonTypeDeleteModel = false;"></Button>
    </div>
  </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import { Card, Input, Table, Switch, Button, Form, Col, Row, Modal, FormItem } from "iview";
import Upload from "@/components/UploadPicture";
import CateImg from "@/assets/images/lesson_default.png";
import Server from '@/service/request';
import Path from '@/service/Path';
export default {
  name: "LowerClassType",
  components: {
    panelTitle,
    Card,
    Input,
    Button,
    "i-table": Table,
    pagination,
    "i-switch": Switch,
    Form,
    Col,
    Row,
    Modal,
    Upload,
    FormItem
  },
  created() {
    //do something after creating vue instance
    this.getClassData(this.current);
  },
  // watch() {
  //   LessonTypeEditModel(val, oldVal) {
  //     if(val == false){

  //     }
  //   }
  // },
  methods: {
    backHistory(){
      this.$router.back(-1);
    },
    deleteItem(id){
      Server.post(Path.deleteCategory,{
        id
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.getClassData(this.current);
          this.$Message.success(res.info);
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    moveItem(id,sort,operate,rank){
      Server.post(Path.shiftCategory,{
        id,
        sort,
        operate,
        rank
      },(res)=>{
        let { code, data } = res;
        if(code === 0){
          this.getClassData();
        }else{
          this.$Message.error(res.info)
        }
      })
    },
    addClassName(){
      let { cateName,CateImgUrl } = this.formData;
      let { id } = this.$route.params;
      Server.post(Path.addCategory,{
        fatherid:id,
        categoryname:cateName,
        imgs: CateImgUrl
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.getClassData(1);
        }else{
          this.$Message.error(res.info);
        }
        this.LessonTypeEditModel = false;
      })
    },
    //
    editClassName(id){
      let { cateName, CateImgUrl } = this.formData;
      Server.post(Path.editCategoryId,{
        id,
        categoryname:cateName,
        imgs: CateImgUrl
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.$Message.success(res.info)
          this.getClassData(this.current);
        }else{
          this.$Message.error(res.info);
        }
        this.LessonTypeEditModel = false;
      })
    },
    isShowClass(id=0,type){
      Server.post(Path.editCategoryId,{
        id,
        status: Number(type),
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.$Message.success(res.info);
        }else {
          this.$Message.error(res.info);
          this.getClassData(this.current);
        }
      })
    },
    pagechange: function(currentPage) {
      this.loading = false;
      this.current = currentPage;
      this.getClassData(currentPage);
      // ajax,  currentPage, 
    },
    getClassData(page=1){
      let { id } = this.$route.params;
      Server.post(Path.getCategoryIdList,{
        'fatherid':id?id:0,'pagenum':page
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.data1 = [];
          let { pagenum, pagesize, total } = data.pageinfo;
          this.total =total;
          this.display=pagesize;
          this.current=Number(pagenum);
          let datas = data.data;
          for (var i = 0; i < datas.length; i++) {
            var obj = {};
            obj.TypeName = datas[i].categoryname;
            obj.Level = datas[i].rankstr;
            obj.TypeNumber = datas[i].juniorcount;
            obj.Order = {sort:datas[i].sort,rank:datas[i].rank};
            obj.isShow = datas[i].status;
            obj.Make = datas[i].id;
            obj.Img = datas[i].imgs;
            this.data1.push(obj);
          }
          this.loading = false;
        }else{
          this.data1 = [];
          this.loading = false;
        }
      })
    },
    ok(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          let { edtiType } = this;
          if(edtiType=='add'){
            this.addClassName();
          }else{
            this.editClassName(this.editId);
          }
          this.LessonTypeEditModel= false;
        }
      })
      
    },
    //
    reset(name){
      this.$refs[name].resetFields();
      this.LessonTypeEditModel = false;
    },
    cancel() {
      console.log("Clicked cancel");
    },
    okDelete() {
      this.deleteItem(this.editId);
      this.LessonTypeDeleteModel = false;
    },
    cancelDelete() {
      this.editId = 0;
    },
    showTypeEditModal(type) {
      if(type=='add'){
        this.formData.cateName = '';
        this.formData.CateImgUrl = CateImg;
        //
        this.LessonTypeText = this.$t("classifyManagepage.Addclassification")
        if(this.$refs.formData!=null){
          this.$refs.formData.resetFields()
        }
      }else{
        //
        this.LessonTypeText = this.$t("classifyManagepage.editclassification")
      }
      this.edtiType = type;
      this.LessonTypeEditModel = true;
    },
    showTypeDeleteModal() {
      this.LessonTypeDeleteModel = true;
      this.getCatergoryCurricu();
    },
    getHeaderUrl(url){
      this.formData.CateImgUrl = url;
      //console.log(this.CateImgUrl);
    },
    //
    getCatergoryCurricu(editId ,current) {
      //console.log('del', this.editId)
      Server.post(Path.getCatergoryCurricu, {
        id: this.editId,
        limit: current
      }, res => {
        if(res.code == 0){
          this.isCourse = true;
          this.datacur = res.data.data;
          this.total = res.data.pageinfo.total;
          this.display = res.data.pageinfo.pagesize;
          this.current = Number(res.data.pageinfo.pagenum);
        }else{
          this.isCourse = false;
          this.datacur = [];
        }
      })
    },
    //
    pagechange(currentPage) {
      this.loading = true;
      this.getCatergoryCurricu(this.editId, currentPage);
      //console.log(currentPage)
    }
  },
  data (){
    const cateRegex = (rule, value, callback) => {
      let tstRegex = /^[a-zA-Z0-9_\u4e00-\u9fa5]+$/
      if (value === '') {
          callback(new Error(''));
      } else if (!tstRegex.test(value)) {
          callback(new Error(''))
      } else {
          callback();
      }
    };
    return {
      
      crumbs: [
        {
        name:this.$route.query.parentName,
        route:this.$route.query.parentRoute,
        },
        {
        name:this.$route.query.currentName,
        route:this.$route.query.currentRoute,
        },
        {
        name:"",
        route:'',
        },
      
      ],
      LessonTypeText:this.$t("classifyManagepage.Addclassification"), //
      total: 0, // 
      display: 10, // 
      current: 1, // 
      loading:true,
      edtiType:'edit',
      editId:0,
      InputText: "",
      LessonTypeEditModel:false,
      LessonTypeDeleteModel:false,
      uploadid:[1,1,1],
      isCourse: false,
      formData: {
        cateName: "", //
        CateImgUrl: CateImg,//
      },
      ruleCustom: {
        cateName: [
          { required: true, validator:cateRegex, trigger: 'blur'}
        ]
      },
      columns1: [
        {
          //
          title: "",
          align: "center",
          key: "Make"
        },
        {
           //
          title: this.$t("classifyManagepage.systematicname"),
          align: "center",
          key: "TypeName"
        },
        {
         // 
          title: this.$t("classifyManagepage.level"),
          align: "center",
          key: "Level"
        },
        {
          //
          title: this.$t("classifyManagepage.Numbersubclasses"),
          align: "center",
          key: "TypeNumber"
        },
        {
          //
          title: this.$t("classifyManagepage.sort"),
          align: "center",
          key: "Order",
          render: (h, params, index) => {
            return h("div", [
              h(
                "a",
                {
                  style: {
                    display: "inline-block",
                    color: "#469af8",
                    marginRight: "10px"
                  },
                  on: {
                    click: value => {
                      let { Make, Order } = params.row;
                      this.moveItem(Make,Order.sort,0,Order.rank);
                    }
                  }
                },
                ""
              ),
              h(
                "a",
                {
                  style: {
                    display: "inline-block",
                    color: "#469af8"
                  },
                  on: {
                    click: value => {
                      let { Make, Order } = params.row;
                      this.moveItem(Make,Order.sort,1,Order.rank);
                    }
                  }
                },
                ""
              )
            ]);
          }
        },
        {
            //
          title:this.$t("classifyManagepage.Whethershown") ,
          align: "center",
          key: "isShow",
          render: (h, params, index) => {
            return h("div", [
              h(
                "i-switch",
                {
                  on: {
                    "on-change": value => {
                      //console.log(params.row.Make, value);
                      this.isShowClass(params.row.Make,value);
                    }
                  },
                  props: {
                    value: Boolean(params.row.isShow)
                  }
                }
              )
            ]);
          }
        },
        {
          //
          title: this.$t("classifyManagepage.operation"),
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h("div", [
              h(
                "div",
                {
                  on: {
                    "click": value => {
                      this.editId = params.row.Make;
                      this.formData.cateName = params.row.TypeName;
                      this.formData.CateImgUrl = params.row.Img;
                      this.showTypeEditModal('edit');
                    }
                  },
                  style: {
                    cursor: "pointer",
                    color: "#2693ff",
                    display: "inline-block",
                    marginRight: "10px"
                  },
                  attrs: {
                    class: "tablebtn blue"
                  }
                },
                ""
              ),
              h(
                "a",
                {
                  on: {
                    "click": value => {
                      this.editId = params.row.Make;
                      this.showTypeDeleteModal();
                    }
                  },
                  style: {
                    cursor: "pointer",
                    color: "red",
                    display: "inline-block"
                  },
                  attrs: {
                    class: "tablebtn red"
                  }
                },
                ""
              )
            ]);
          }
        }
      ],
      columnscur: [{
        title: "",
        align: "center",
        key: "id"
      },{
        title: "",
        align: "center",
        key: "coursename"
      }],
      data1: [],
      datacur: []
    }

  }
};
</script>
<style lang="less" scoped>
@import "~@/assets/less/tab.less";
  .title-page span{
    cursor: pointer;
  }
  .must{
  color: #f00;
}
</style>



