<template>
  <div id="Course">
     <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="mytheme-mg-top-20">
          <!---->
          <span>{{$t('Common.colon')}}</span>
          <!---->
          <Input
            v-model.trim="InputText"
            placeholder=""
            @on-enter="getStuList(InputText, nickname, current)"
           class="input-normal-width input-max-height"/>
          <!---->
          <span class="mytheme-mg-left-70">{{$t('Common.colon')}}</span>
          <!---->
          <Input
            v-model.trim="nickname"
            placeholder=""
            @on-enter="getStuList(InputText, nickname, current)"
           class="input-normal-width input-max-height"/>

          <Button type="primary" class="mytheme-mg-left-70" @click="getStuList(InputText, nickname, current)"></Button>

          <!--\ -->
          <div class="fg">
            <!-- <Button type="primary"  @click="addStudentModel"></Button> -->
            <Button type="primary" @click="showPush"></Button>
         </div>
      </div>

      <!--  -->
      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe ref="selection" :loading="loading" :columns="columns1" :data="data1" @on-selection-change="chageSelect"></i-table>
      
       <!--  -->
      <pagination :total="total" :display="display" class="mg-top-20" :current-page='current' @pagechange="pagechange" v-if='total>0'></pagination>
   </Card>

     <!--  -->
    <Modal v-model="isPush" width="560" :closable="false">
      <div slot="header"></div>
       <Form :label-width="127" ref="pushInfo" :model="pushInfo" :rules="ruleValidate">
          <Row>
            <Col>
              <FormItem label="" prop="title">
                  <Input type="text" v-model.trim="pushInfo.title" placeholder="14" :maxlength="14" style="width: 340px;"/>
              </FormItem>
            </Col>
          </Row>
           <Row>
            <Col>
              <FormItem label="" prop="content">
                  <Input type="textarea" v-model.trim="pushInfo.content" placeholder="50" :maxlength="50"  style="width: 340px;" :rows="5"/>
              </FormItem>
            </Col>
          </Row>
      </Form>
      <div slot="footer" style="text-align: center;">
          <!-- -->
          <Button type="primary" @click="sendStudentMessage('pushInfo')">{{$t('Common.sure')}}</Button>
          <!-- -->
          <Button @click="handleReset('pushInfo')">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";
import {
  Card,
  Input,
  Table,
  Switch,
  Button,
  Form,
  Row,
  Col,
  FormItem,
  Checkbox,
  Modal
} from "iview";
export default {
  name: "",
  components: {
    panelTitle,
    pagination,
    Card,
    Input,
    "i-table": Table,
    "i-switch": Switch,
    Button,
    Form,
    Row,
    Col,
    FormItem,
    Checkbox,
    Modal
  },
  created() {
    this.getStuList(this.mobile, this.nickname, this.current);
  },
  watch: {
  },
  methods: {
    //
    getStuList(mobile, nickname, page = 1) {
      Server.post(
        Path.getStudentUserList,
        {
          mobile: mobile,
          nickname: nickname,
          pagenum: page
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            // this.data1 = [];
            let { pagenum, pagesize, total } = data.pageinfo;
            this.total = total;
            this.display = pagesize;
            this.current = Number(pagenum);
            this.data1 = data.data;
            this.loading = false;
          } else {
            this.data1 = [];
            this.loading = false;
          }
        }
      );
    },
    //
    pagechange(currentPage) {
      this.getStuList(this.mobile, this.nickname, currentPage);
    },
    chageSelect(val) {//id
    let arr=[]
      val.forEach(el => {
        arr.push(el.id)
      });
      this.studentids=arr
    },
    handleSelectAll(status) {//
      this.$refs.selection.selectAll(status);
    },
    sendStudentMessage(name) {//
     this.$refs[name].validate(valid => {
        if (valid) {
          Server.post(
            Path.sendStudentMessage,
            {
              studentids: this.studentids,
              title: this.pushInfo.title,
              content: this.pushInfo.content
            },res=>{
              if(res.code==0){
                this.isPush=false;
                this.studentids==[]
                this.handleSelectAll(false)
              this.$Message.success(res.info);
              }
            })
        }
     })
    },
     handleReset(name) {
      //
      this.$refs[name].resetFields();
      this.$refs.selection.selectAll(false);
      this.isPush=false;
      this.studentids=[];
    },
    //
    showPush(){
      if(this.studentids.length==0){
        this.$Message.error("");
        return false;
        }else{
        this.isPush=true;
      }
      this.pushInfo.title="";
      this.pushInfo.content="";
    },
    // handleCheckAllhandleCheckAll() {
    //   if (this.indeterminate) {
    //     this.AllCheck = false;
    //   } else {
    //     this.AllCheck = !this.AllCheck;
    //   }
    //   this.indeterminate = false;
    //   if (this.AllCheck) {
    //     this.handleSelectAll(true);
    //   } else {
    //     this.handleSelectAll(false);
    //   }
    // },
    
    // addStudentModel() {
    //   //
    //   let id = JSON.parse(window.localStorage.getItem("userinfo")).organid;
    //   let { store } = this.$store;
    //   store.dispatch({
    //     type: "getStatus",
    //     id,
    //     newtype: "-1"
    //   });
    // },
  },
  data() {
    return {
      // mobile: "",
      nickname: "",//
      InputText: "",//
      loading: true,//
      total: 0, // 
      display: 20, // 
      current: 1, // 
      isPush:false,//
      studentids:[],//ID
      pushInfo:{
        title:'',//
        content:''//
      },
      ruleValidate: {
        title: [{ required: true, message: '', trigger: 'blur' }],
        content: [{ required: true, message: '', trigger: 'blur' }]
      },
      columns1: [
        {
          type: "selection",
          width: 60,
          align: "center"
        },
        {
          title: "ID", //ID
          align: "center",
          key: "id"
        },
        {
          title: "", //
          key: "mobile",
          align: "center"
        },
        {
          title: "", //
          align: "center",
          key: "nickname"
        },
        {
          title: this.$t("studentManagepage.sex"), //
          align: "center",
          key: "sex",
          render: (h, params, index) => {
            let Sex = params.row.sex;
            switch (Sex) {
              case 0:
                Sex = this.$t("teacherDetails.unknow"); //
                break;
              case 1:
                Sex = this.$t("teacherDetails.male"); //
                break;
              case 2:
                Sex = this.$t("teacherDetails.female"); //
                break;
            }
            return h("div", {}, Sex);
          }
        },
        {
          title: "", //
          align: "center",
          key: "categoryname",
          render: (h, params) => {
            return h("div", params.row.categoryname || "---");
          }
        },
        {
          title: this.$t("studentManagepage.Recently"), //
          align: "center",
          key: "logintime"
        },
        {
          title: "", //
          // width: 300,
          align: "center",
          render: (h, params) => {
            let remark = "---";
            if (params.row.tagname && params.row.childtagname) {
              remark = params.row.tagname + "" + params.row.childtagname;
            }
            return h("div", remark);
          }
        },
        {
          title: "",
          align: "center",
          key: "status",
          render: (h, params, index) => {
            return h("div", [
              h("i-switch", {
                on: {
                  "on-change": value => {
                    Server.post(
                      Path.changeUserStatus,
                      {
                        userid: params.row.id,
                        flag: !Number(value)
                      },
                      res => {
                        let { code, data } = res;
                        if (code === 0) {
                          this.$Message.info(res.info);
                        } else {
                          this.$Message.error(res.info);
                        }
                      }
                    );
                  }
                },
                props: {
                  value: !Boolean(params.row.status)
                }
              })
            ]);
          }
        },
        {
          title: this.$t("Common.mark"), //
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h(
              "router-link",
              {
                props: {
                  to:
                    "/StudentInfo/" + params.row.id + "?from=MainUserManagement"
                },
                style: {
                  cursor: "pointer",
                  color: "#3F9CE8",
                  display: "inline-block",
                },
                attrs: {
                  class: "tablebtn yellow"
                }
              },
              ""
            ); //
          }
        }
      ],
      data1: []
    };
  }
};
</script>
<style scoped lang='less'>
</style>



