<template>
  <div>
    <panel-title :crumbs="routeArr" />
    <Tabs class='mytheme-tab' type="card" :animated="false" @on-click='msg' >
          <TabPane label="" name=''>
              <pending-course></pending-course>
          </TabPane>
          <TabPane label="" name=''>
            <processed-course></processed-course>
          </TabPane>
      </Tabs>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import {Tabs, TabPane} from "iview";
import pendingCourse from "./pendingCourse";
import processedCourse from "./processedCourse";
export default {
  name: "AdjustingCurriculumSchedule",
  components: {panelTitle,Tabs,
    TabPane,pendingCourse,processedCourse},
   data(){
    return {
      routeArr:[{name:'',route:''},{name:'',route:''}]
    }
  },
  methods: {
      msg(name){
     this.routeArr=[{name:'',route:''},{name:name,route:''}];
    }
  },
  created() {
  
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>

</style>



