<template>
  <div>
    <!--$t('classifyManagepage.classifyManagepage')-->
    <panel-title
      :crumbs="[
        {
          name:'',
          route:{
            name:'RewardSet',
            query:{from:'TeachingAffairs'}
          }
        },
        {
          name:titleName, //
          route:'',
        },
      ]"
    />
    <!--  -->
    <Card dis-hover class="h-new-search"  v-if="titleName==''">
      <Form ref="formInline"   :model="formInline" :rules="ruleValidate" :label-width="180" class='form-confrol cardp1'>

        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="" prop="rewardName">
              <Input v-model="formInline.rewardName" placeholder=""  ></Input>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <!---->
            <FormItem label="" prop="dayTime">
              <span class="text">&nbsp;&nbsp;>=&nbsp;&nbsp;<Input v-model="formInline.dayTime"   style="width:80px;" ></Input>&nbsp;&nbsp;</span>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="12" offset="2">
            <!---->
            <FormItem  prop="allDayCurrent">
              <span class="text">&nbsp;&nbsp;>=&nbsp;&nbsp;<Input v-model="formInline.allDayCurrent"   style="width:80px;" ></Input>&nbsp;&nbsp;</span>
              <div class='headimg-des'></div>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem  label="" prop="radioInfo">
              <RadioGroup v-model="formInline.radioInfo">
                <Radio label="1"></Radio>&nbsp;&nbsp;
                <Radio label="2" ></Radio>&nbsp;&nbsp;
              </RadioGroup>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="" prop="discountName">
              <Input v-model="formInline.discountName" placeholder=""  ></Input>
            </FormItem>
          </Col>
        </Row>
        <!-- v-if=''-->
         <!-- <Input v-else v-model="formInline.money" placeholder=""></Input> -->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="" prop="money">
              <Input v-model="formInline.money" :placeholder="formInline.radioInfo==2?'':''" >
                <span slot="append">{{formInline.radioInfo==2?'':''}}</span>
              </Input>
              <div class='headimg-des'>0.01-100002</div>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem  label=""  prop="thresholdMoney">
              <RadioGroup v-model="formInline.threshold">
                <!-- 01 -->
                <Radio label="0" ></Radio>&nbsp;&nbsp;
                <Radio label="1"></Radio>&nbsp;&nbsp;
              </RadioGroup>
              <Input  v-model="formInline.thresholdMoney"  :disabled="formInline.threshold=='0'"  style="width:80px;" ></Input>
              &nbsp;&nbsp;<span class="text"></span>
            </FormItem>
          </Col>
        </Row>

        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label=""   prop="dateNumberTime">
              <RadioGroup v-model="formInline.radioGroup">
                <Radio label="1"></Radio>{{formInline.dateRangeTime}}
                &nbsp;<DatePicker :disabled="formInline.radioGroup=='2'"  type="date" placeholder=""  @on-change="getTime"></DatePicker>
                <br>
                <br>
                <Radio label="2"></Radio>
                &nbsp;<Input :disabled="formInline.radioGroup=='1'"   v-model="formInline.dateNumberTime" placeholder="" style="width:80px;" ></Input>
                <span class="text">
                  &nbsp;&nbsp;
              </span>
              </RadioGroup>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="" prop="courseType">
              <RadioGroup v-model="formInline.courseType">
                <Radio label="" ></Radio>&nbsp;&nbsp;
                <Radio label=""></Radio>&nbsp;&nbsp;
                <Radio label=""></Radio>&nbsp;&nbsp;
              </RadioGroup>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="" >
              <span class="text">
                    <span style="color:green;" @click="openModal" class="cursorChange">&nbsp;&nbsp;&nbsp;</span>
              </span>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="" prop="textareaInfo">
              <Input  v-model="formInline.textareaInfo" style="width:300px;" type="textarea" :rows="9" placeholder=""></Input>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="20" offset="2" class="mg-bottom-20" style="margin-top:20px ;">
            <FormItem label="">
              <div class="custom-btn-warning savebtn" @click="formSubmit('formInline')">
                
              </div>
              <!-- <div class="btn savebtn" @click="SetBascInfo"></div> -->
            </FormItem>
          </Col>
        </Row>
      </Form>
      <!---->
      <Modal
        v-model="modalStatus"
        title="" >
        <div class="type-content">
           <!-- v-model="periodId" filterable -->
          <Select placeholder="" style="width:300px;border: 1px solid #dddee1;border-radius: 4px;">
             <!-- v-for="item in classifyTow" :value="item.id" :key="item.id" -->
              <Option value="1"></Option>
          </Select>
          <Tree :data="data1" :getSelectedNodes="getSelectedNodes"  show-checkbox></Tree>
        </div>
        <div slot="footer">
          <!---->
          <Button type="text" shape="circle" @click="modalStatus = false;">{{$t('Common.cancel')}}</Button>
          <!---->
          <Button type="warning" shape="circle" >{{$t('Common.sure')}}</Button>
        </div>
      </Modal>
    </Card>

    <!--  -->
    <Card dis-hover class="h-new-search"  v-if="titleName==''">
      <Form :label-width="180" class='form-confrol'>
        <!--  -->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="">
              <span class='text'>{{rewardInfo.name}}</span>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="">
              <span class='text'>&nbsp;&nbsp;>=&nbsp;&nbsp;{{rewardInfo.condition1}}&nbsp;&nbsp;</span>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="12" offset="2">
            <FormItem >
              <span class='text'>&nbsp;&nbsp;>=&nbsp;&nbsp;{{rewardInfo.condition2}}&nbsp;&nbsp;</span>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="">
              <span class='text'>{{rewardInfo.type=='1'?'':''}}</span>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="">
              <span class='text'>{{rewardInfo.name}}</span>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="">
              <span class='text'>{{rewardInfo.value}}&nbsp;&nbsp;</span>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="">
              <span class='text'>{{rewardInfo.mixamount}}</span>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="">
              <span class='text'> &nbsp;&nbsp;&nbsp;{{rewardInfo.expirevalue}}</span>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="">
              <span class='text'>{{rewardInfo.forcourse}}</span>
            </FormItem>
          </Col>
        </Row>
      </Form>
    </Card>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import { Card,  Table, Button,Modal,Form,Row,Col,Input,FormItem,RadioGroup,Radio,Icon,DatePicker,Tree,Select,Option} from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "",
  components: {panelTitle,pagination,Card,'i-table':Table, Button,Modal,Form,Row,Col,Input,FormItem,RadioGroup,Radio,Icon,DatePicker,Tree,Select,Option},
   data(){
     const DayTimeRegex = (rule, value, callback) => {
       if(this.formInline.threshold!='1'){
         return;
       }
       let DayTimeRegex = /^[1-9]\d*$/;
       if (value === '') {
         callback(new Error(''));
       } else if (!DayTimeRegex.test(value)) {
         callback(new Error('0'))
       } else {
         callback();
       }
     };
     const DateNumberTimeRegex = (rule, value, callback) => {
       if(this.formInline.radioGroup!='2'){
         return;
       }
       let DayTimeRegex = /^[1-9]\d*$/;
       if (value === '') {
         callback(new Error(''));
       } else if (!DayTimeRegex.test(value)) {
         callback(new Error('0'))
       } else {
         callback();
       }
     };
     const EmptyRegex = (rule, value, callback) => {
       if (value === '') {
         callback(new Error(''));
       } else {
         callback();
       }
     };
     const AllDayCurrentRegex = (rule, value, callback) => {
       let AllDayCurrent = /^[1-9]\d*$/;
       if (value === '') {
         callback()
       } else if (!AllDayCurrent.test(value)) {
         callback(new Error('0'))
       }
     };
     const MoneyRegex = (rule, value, callback) => {
       let  MoneyRegex = /(^[1-9]\d{0,3}$)|(^[1][0]{4}$)|(^0\.\d{0,2}$)|(^[1-9]\d{0,3}\.\d{0,2}$)/;
       if (value === '') {
         callback(new Error(''));
       } else if (!MoneyRegex.test(value)) {
         callback(new Error(''))
       }
     };
    return {
      value1: [],
      data1: [
        {
          title: 'parent 1',
          expand: true,
          children: [
            {
              title: 'parent 1-1',
              expand: true,
              children: [
                {
                  title: 'leaf 1-1-1'
                },
                {
                  title: 'leaf 1-1-2'
                }
              ]
            },
            {
              title: 'parent 1-2',
              expand: true,
              children: [
                {
                  title: 'leaf 1-2-1'
                },
                {
                  title: 'leaf 1-2-1'
                }
              ]
            }
          ]
        }],
      rewardInfo:{},
      titleName:'',
      modalStatus:false,
      formInline:{
        rewardName:'', //
        dayTime:'',     //
        allDayCurrent:"",   //
        radioInfo:"1",  //12
        discountName:'',    //
        money:'',       //
        threshold:'1',    // 01
        thresholdMoney:'',  //
        dateRange:false,  //
        dateNumber:'',  //
        dateRangeTime:'', //
        dateNumberTime:'', //
        textareaInfo:'',   //
        courseType:'',  //
        radioGroup:'2'//1, 2
      },
      ruleValidate:{
        rewardName:[  { required: true, validator:EmptyRegex} ],
        dayTime:[  { required: true, validator:DayTimeRegex }],
        allDayCurrent:[  { validator:AllDayCurrentRegex} ],
        discountName:[  { required: true,validator:EmptyRegex } ],
        money:[  { required: true, validator:MoneyRegex }, ],
        thresholdMoney:[  { required: true, validator:DayTimeRegex } ],
        dateRangeTime:[  { required: true, validator:EmptyRegex }],
        dateNumberTime:[  { required: true, validator:DateNumberTimeRegex} ],
        textareaInfo:[  { required: true, validator:EmptyRegex } ],
      }
    }
  },
  methods: {
    getTime(timer) {//
      this.formInline.dateRangeTime = timer;
    },
    getSelectedNodes(value){  //
      console.log(value);
    },
    openModal(){    //
      this.modalStatus = true;
    },
    formSubmit(name){  //
        Server.post(Path.addReward,{
            name:this.formInline.rewardName,
						type:this.formInline.radioInfo,
						condition1:this.formInline.dayTime,
						condition2:this.formInline.allDayCurrent,
						value:this.formInline.money,
						mixamount:this.formInline.threshold==0?'0':this.formInline.thresholdMoney,
						expiretype:this.formInline.radioGroup,
						expirevalue:this.formInline.dateRangeTime||this.formInline.dateNumberTime,
						forcourse:'',
						note:this.formInline.textareaInfo
        },
        res => {
          if (res.code === 0) {
            this.$Message.info(res.info);
          }
        }
      );
    },
    //
      getRewardinfo(){
        Server.post(Path.getRewardinfo,{
          id:this.$route.params.id
        },
        res => {
          if (res.code === 0) {
            this.rewardInfo=res.data;
          }
        }
      );
      this.deleteItemModal = false;
    },
  },
  created() {
    let fromPage = this.$route.params.type;
    if(fromPage=='add'){
      this.titleName = '';
    }else{
      this.titleName = '';
       this.getRewardinfo()
      let nowInfo = JSON.parse(this.$Local.getItem('rewardInfo'));
      this.rewardInfo = nowInfo;
    }
  },
 mounted(){},
 computed:{},
 watch:{}
 
};
</script>
<style scoped>
   /* @import "~@/assets/common.css"; */
 .cursorChange:hover
 {
   cursor:Pointer
 }

.cardp1 .ivu-form-item-validating .ivu-input-icon-validate {
  display: none;
}
/* .cardp1 .ivu-form-item-error .ivu-input-group-prepend, .ivu-form-item-error .ivu-input-group-append{
  border-color:#CFDCE7;
} */
.cardp1 .ivu-icon .ivu-icon-load-c .ivu-load-loop .ivu-input-icon .ivu-input-icon-validate{
  display: none!important;
}
.cardp1 .ivu-checkbox-wrapper + span, .ivu-checkbox + span{
	display: none;
}
</style>



