<template>
  <div>
    <panel-title :crumbs="[{name:'',route:''}]" />
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
       <div class="mytheme-mg-top-20" style="position: relative;">
          <!---->
          <span></span>
          <!-- -->
          <Input class="input-normal-width input-max-height"
                v-model.trim="contentName"
                @on-enter="getTableInfo(current)"
                placeholder=""/>
          <Button type="primary" class="mytheme-mg-left-70" @click="getTableInfo(current)"></Button>

          <!--/-->
           <div class="butGroup">
            <Button type="primary" class="mytheme-mg-right-50" @click="typeOperate('addType','')"></Button>
             <Upload 
              style="display: inline-block;"
              name="uploadFile"
              :action="upload.Path"
              :data='upload.data'
              :format="upload.format"
              :on-success="onsuccess"
               :headers="head"
              @click.native="setHeader(upload.data)"
              :on-error="onError">
               <!-- @click="openToLead" -->
                <Button type="warning" ></Button>
            </Upload>
          </div>
         </div>
      <!---->
      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe ref="selection" :loading=false :columns="colData" :data="tabData"></i-table>
      <!---->
      <pagination  class="mg-top-20" :total="total" :currentPage="current" :display="display" @pagechange="pagechange" v-if='total>0'></pagination>
    </Card>
   
    <!---->
    <Modal
      v-model="deleteItemModal"
          :closable="false">
        <div slot="header"></div>
      <span></span>
      <div slot="footer">
        <!---->
        <Button type="primary" @click="okDelete">{{$t('Common.sure')}}</Button>
        <!---->
        <Button @click="deleteItemModal = false;">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>

    <!---->
    <Modal
      v-model="isShowEditModal"
      :closable="false">
        <div slot="header">{{LessonTypeText}}</div>
      <Form  ref="formInline"  :label-width="120" :model="formInline" :rules="ruleValidate">
        <!---->
        <Row>
          <Col>
            <FormItem prop="radioInfo" label="">
              <RadioGroup v-model="formInline.radioInfo">
                <Radio :label="i.id" v-for="i in radioArr" :key='i.id'>{{i.name}}</Radio>
              </RadioGroup>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row >
          <Col >
            <FormItem prop="textareaInfo" label="" >
              <Input  v-model="formInline.textareaInfo" type="textarea" :rows="6" placeholder=",60" :maxlength="60"></Input>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row  >
          <Col>
            <FormItem prop="value" label="">
              <Input v-model="formInline.value" placeholder=",10" :maxlength="10" ></Input>
            </FormItem>
          </Col>
        </Row>
        <!---->
        <Row  >
          <Col>
            <FormItem prop="forstudenttype" label="">
               <Select v-model="formInline.forstudenttype" filterable placeholder="" style="width:100%;border: 1px solid #dddee1;border-radius: 4px;">
                  <Option v-for="item in forstudenttype" :value="item.id" :key="item.id">{{item.name}}</Option>
              </Select>
            </FormItem>
          </Col>
        </Row>
      </Form>

      <div slot="footer">
        <!---->
        <Button type="primary" @click="ok('formInline')">{{$t('Common.sure')}}</Button>
        <!---->
        <Button @click="isShowEditModal = false;">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import JsonInfo from '@/assets/testInfo/KnowledgeManagementInfo';
import { Card, Input,Row,Col ,Table, Button,Modal,RadioGroup,Radio,Select,Option,Form,FormItem,Upload} from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "",
  components: {panelTitle,Row,Col, Card,Input,"i-table": Table,Button,Modal,pagination,RadioGroup,Radio,Select,Option,Form,FormItem,Upload},
   data(){
     const EmptyRegex = (rule, value, callback) => {
       if (value === '') {
         callback(new Error(''));
       } else {
         callback();
       }
     };
     const studentTypeRegex = (rule, value, callback) => {
       if(!this.forstudenttype.length){
         callback(new Error(''));
       }else if (value === '') {
         callback(new Error(''));
       } else {
         callback();
       }
     };
    return {
       upload:{//
        Path: window.location.origin === 'http://127.0.0.1:8080' ? "http://www.lemonedu.com/admin/Importdata/Import" : "/admin/Importdata/Import",
        data:{importtype:2},
        format:['xlsx','xls','xlsm','xltx','xltm','xlsb','xlam']
      },
      head:{
        sign:'',
        token:'',
        starttime:'',
      },
      contentName:'',
      LessonTypeText:'',  //
      isShowEditModal:false,  //
      deleteItemModal:false,   //
      total: 0, // 
      display: 10, // 
      current: 1, // 
      name:'',  //
      deleteId:null,   //ID
      modalType:'modify',  //
      radioArr:[],//
      forstudenttype:[],//
        //
      formInline:{
        textareaInfo:'',
        value:'',
        radioInfo:'',
        forstudenttype:''
      },
      classifyOne:[],
      ruleValidate:{  //
        textareaInfo:  [  { required: true, validator:EmptyRegex } ],
        // value:  [  {validator:EmptyRegex} ],
        radioInfo:[  { required: true, validator:EmptyRegex } ],
        forstudenttype:[  { required: true, validator:studentTypeRegex } ],
      },
      colData: [
        // {
        //   type: 'selection',
        //   align: "center",
        //   width:60
        // },
        {
          title: '',
          key: 'id',
          align: "center"
        },
        {
          title: '',
          align: "center",
          key: 'typename',
        },
        {
          title: '',
          key: 'content',
          align: "center"
        },
        {
          title: '',
          key: 'answer',
          align: "center"
        },
        {
          title: '',
          key: 'forstudenttypename',
          align: "center"
        },
        {
          title: '',
          key: 'addtime',
          align: "center"
        },
        {
          title: '',
          render: (h, params) => {
            return h("div", [
              h("a",{
                style: {
                  color: "#3F9CE8",
                  display: "inline-block",
                  marginRight: "35px"
                },
                on: {
                  'click': () => {
                    // ID
                    this.deleteId = params.row.id;
                     Server.post(
                          Path.getKnowledgemsg,
                          {
                           id:this.deleteId
                          },
                          res => {
                            if(res.code==0){
                              this.formInline={
                                  textareaInfo:res.data.content,
                                  value:res.data.answer,
                                  radioInfo:res.data.typeid,
                                  forstudenttype:res.data.forstudenttype
                                };
                            }
                     });
                    // // rows
                    let typeName = params.row.type;
                    this.typeOperate('modify',typeName);
                  }
                }
              },""),
              h("a",{
                style: { color: "#ED3825", display: "inline-block"},
                on: {
                  'click': () => {
                    // ID
                    this.deleteId = params.row.id;
                    // 
                    this.deleteItemModal = true;
                  }
                }
              },"") //
            ]);
          },
          align: "center"
        }
      ],
      tabData: [

      ]
    }
  },

  methods: {
     //
    setHeader(data) {
      this.head = this.$Utils.setHeader(data);
    },
     onsuccess(res) {//
     if (!this.radioArr.length) {
          this.$Message.success("");
          return false;
        }
        let {code,data,info}=res
        if(code==0){
          this.getTableInfo(this.current);
          let text=""+data.num+""
          this.$Message.success(text);
        }else if(code==70210){
            this.$Message.warning(info);
        }
    },
     onError(res) {//
        let {code,info}=res
        if(code==70411){
          this.$Message.warning(info);
        }
    },
    getTableInfo(current=1) {  //
      Server.post(Path.getKnowledgeList,{
          content: this.contentName,
          pagenum: current,
        },
        res => {
          if (res.code === 0||res.code ===70200) {
            this.tabData = res.data.data;
            this.tabData.forEach(element => {
              if (element.answer=="") {
                element.answer="---";
              }
              if (element.forstudenttypename==null) {
                element.forstudenttypename="---";
              }
            });
            this.total = res.data.pageinfo.total;
            this.display = res.data.pageinfo.pagesize;
            this.current = Number(res.data.pageinfo.pagenum);
          }else{
            this.tabData = [];
            this.total = 0;
            this.display = 10;
            this.current = 1;
          }
        }
      );
    },
    pagechange(currentPage) { //
      this.getTableInfo(currentPage);
    },
    okDelete(){// 
       Server.post(Path.deleteKnowledge,{
        id:this.deleteId,
        },
        res => {
          if (res.code ===0) {
            this.$Message.success(res.info);
            this.deleteItemModal = false;
            this.getTableInfo(this.current);
          }
        }
      );
    },
    getAllKnowledgeTypeList(){//
       Server.post(Path.getAllKnowledgeTypeList,{},
        res => {
          if (res.code ==0||res.code===70101) {
            this.radioArr = res.data;
            this.formInline.radioInfo=this.radioArr[0].id;
          }
        }
      );
    },
    getAllCategoryList(){//
       Server.post(Path.getAllCategoryList,{},
        res => {
          if (res.code ==0) {
           this.forstudenttype=res.data
          }
        }
      );
    },
    typeOperate(type,typeName){
       if (!this.radioArr.length) {
          this.$Message.success("");
          return false;
        }
        
      // 
      this.modalType = type;
      // 
      
      this.getAllCategoryList();
      if(type=='modify'){
        this.radioInfo=typeName;
      }else{
        this.formInline.radioInfo=this.radioArr[0].id;
        this.formInline.textareaInfo = '';
        this.formInline.value= '';
        this.formInline.forstudenttype= '';
      }
      this.isShowEditModal = true;
    },
    ok(name){
      this.$refs[name].validate((valid) => {
        if (valid) {
          // 
          if(this.modalType=='modify'){
            Server.post(Path.updateKnowledge,{
                id: this.deleteId,
                typeid: this.formInline.radioInfo,
                content: this.formInline.textareaInfo,
                answer: this.formInline.value,
                forstudenttype:this.formInline.forstudenttype
              },
              res => {
                if (res.code === 0) {
                  this.getTableInfo(this.current);
                  this.$Message.success(res.info);
                }
              }
            )
          }else{
            Server.post(Path.addKnowledge,{
                typeid: this.formInline.radioInfo,
                content: this.formInline.textareaInfo,
                answer: this.formInline.value,
                forstudenttype:this.formInline.forstudenttype
              },
              res => {
                if (res.code === 0) {
                  this.getTableInfo(this.current);
                  this.$Message.success(res.info);
                }
              }
            )
          }
          // 
          this.isShowEditModal = false;

        }
      })
    },
  },
  created() {
    this.getAllKnowledgeTypeList();
    this.getTableInfo(this.current);
  },
 mounted(){

 },
 computed:{

 },
 watch:{
   isShowEditModal(val){
      if(val==false){
        this.$refs.formInline.resetFields()
      }
    }
 }
};
</script>
<style scoped lang='less'>
.butGroup{
  position: absolute;
  top: 0;
  right:0;
  display:flex;
  align-items: flex-start;
}
</style>



