          </Row>

           <!--  -->
          <Modal v-model="isShowDel" width="900" :closable="false">
					    	    <div slot="header">	</div>
                    <Card dis-hover class="mytheme-noborder">
		        				<div style=" height: 300px;">
		        					<ul class="classUl">
		        						 <CheckboxGroup  class="checkboxGroup" v-model="cheecck"  @on-change="getDate">
			        						<li v-for="(itmes,index) in dataM" :key="index" class="claasLi">
