					      </div>
              
					</Modal>

						<!---->
          <Modal v-model="isShowDell" width="900" :closable="false">
                <div slot="header"> </div>
                 <Card dis-hover class="mytheme-noborder">
                <div style=" height: 300px;">
                  <ul class="classUl">
                      <CheckboxGroup  class="checkboxGroup" v-model="cheeecck"  @on-change="getDatePlus">
                      <li v-for="(itmes,index) in dataw" :key="index" class="claasLi">
                          <img :src="itmes.imageurl" alt="" />
                          <!-- <span class="classSpan">{{itmes.coursename}}</span> -->
                            <Checkbox class="classBox" :label="itmes.id" v-model="itmes.focus" size="large" >{{itmes.coursename}}</Checkbox>
                      </li>
                      </CheckboxGroup>
