                </div>
       					<pagination :total="totall"  :current-page='currentl' @pagechange="pagechangePlus"  :display= "displayl" v-if='totall>0'></pagination>
		        		</Card>			
					      <div slot="footer" >
					    		<Button type="primary" @click="isShowDell=false;edit1=true;"></Button>
					        <Button @click="isShowDell= false"></Button>
					      </div>
					</Modal>
					
					<!---->
			    <Modal v-model="isShowDelModal" width="500" :closable="false">
			    	     <div slot="header"></div>
            	 <Tree :data="data1" ref="mTree" multiple show-checkbox v-model="formItem.tree" @on-check-change= "setTree();"></Tree>
				      <div slot="footer">
				    		<Button type="primary" @click="isShowDelModal=false; setTree()"></Button>
				        <Button @click="moisShow('mian')" ></Button>
				      </div>
			    </Modal>
			    
					<!---->
			    <Modal v-model="isShowDelModall" width="500" :closable="false">
			    	     <div slot="header"></div>
            	 <Tree :data="data2" ref="mTreePlus" multiple show-checkbox v-model="formItem.treePlus" @on-check-change= "setTreePlus();"></Tree>
				      <div slot="footer">
				    		<Button type="primary" @click="isShowDelModall=false; setTreePlus()"></Button>
				        <Button @click="moisShow('plus')"></Button>
				      </div>
			    </Modal>

        <!---->  
				<div v-if="this.check == 'female' ? false : true">  
			  		  <Row>
		            <Col span="12" offset="1">
		            <!--   -->
		              <FormItem label="" >
				        		<RadioGroup v-model="give" class="term" style="margin-top: 3px;">
		                    <FormItem label="" prop="broadcast" :show-message="maleBol4">
		                    	<Radio label="broadcast" style="margin-bottom: 7px;"></Radio>
				            			<Input  style="width: 18%;margin: 0 10px 6px 0" :disabled="this.give == 'broadcast' ? false : true" v-model="broadcast"/>&nbsp;
				            		</FormItem>
				            		</br>
				            		 <FormItem label="" prop="broadcasting" :show-message="maleBol5">
		                      	<Radio label="broadcasting" style="margin-top: 4px;"></Radio>
				            				<Input  style="width: 18%;margin:0px 10px 0  0; display: inline-block;" :disabled="this.give == 'broadcasting' ? false : true " v-model="broadcasting"/>&nbsp;
				           			 </FormItem>
				        		</RadioGroup>
