		              				<span v-if="activePlus==2" class="classification edit" @click = "isShowDelModall = true;active1 ('Appoint1',1) ">{{classifyNum1}}</span>
		              				<span v-if="activePlus==3" class="classification edit" @click = "isShowDell = true; active1 ('fication1',2) ">{{stragePlus.length}}</span>
