</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import cardTitle from "@/components/common/CardTitle/CardTitle";
import {
  Card,
  Form,
  FormItem,
  Input,
  Upload,
  Button,
  Col,
  Row,
  RadioGroup,
  Radio,
  DatePicker,
  Modal,
  Select,
  Option,
  Tree,
  CheckboxGroup,
  Checkbox
} from "iview";
import Server from "@/service/request";
import UploadPic from "@/components/UploadPicture";
import Path from "@/service/Path";
import uploadIcon from "@/assets/images/icon_addfolder@2x.png";
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Form,
    FormItem,
    UploadPic,
    Input,
    Upload,
    Button,
    Col,
    Row,
    pagination,
    cardTitle,
    RadioGroup,
    Radio,
    DatePicker,
    Modal,
    Select,
    Option,
    Tree,
    CheckboxGroup,
    Checkbox
  },
  data() {
    const PriceMeal = (rule, value, callback) => {
      //
      let PriceRegex = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
      if (value === "") {
        callback(new Error(""));
      } else if (!PriceRegex.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const mealPrice = (rule, value, callback) => {
      //
      let PriceRegex = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
      if (this.threshold === "male") {
        callback();
      } else {
        if (value === "") {
          callback(new Error(""));
        } else if (!PriceRegex.test(value)) {
          callback(new Error(""));
        }
        callback();
      }
    };
    const classPrice = (rule, value, callback) => {
      //
      let PriceRegex = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
      value = this.formItem.classPrice1;
      if (this.thresholdPlus === "female" && value === "") {
        callback(new Error(""));
      } else if (this.thresholdPlus === "male") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const classMeal = (rule, value, callback) => {
      //
      let PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (value === "") {
        callback(new Error(""));
      } else if (!PriceRegex.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const images = (rule, value, callback) => {
      //
      if (value === "") {
        callback(new Error("log"));
      }
      callback();
    };
    const classNum = (rule, value, callback) => {
      //
      let PriceRegex = /^[1-9][0-9]{0,4}$/;
      if (this.radipurchaseo === "female" && value === "") {
        callback(new Error(""));
      } else if (this.radipurchaseo != "female") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const numDays = (rule, value, callback) => {
      //
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.term === "days" && value === "") {
        callback(new Error(""));
      } else if (this.term != "days") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const treeData = (rule, value, callback) => {
      //
      value = this.treeData;
      if (this.activeIdex === 2 && value.length == 0) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const treeDataPlus = (rule, value, callback) => {
      //
      value = this.treeDataPlus;
      if (this.activePlus === 2 && value.length == 0) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const Daysnum = (rule, value, callback) => {
      //
      value = this.formItem.Daysnum;
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.termPlus === "days" && value === "") {
        callback(new Error(""));
      } else if (this.termPlus != "days") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const broadcasting = (rule, value, callback) => {
      //--
      value = this.broadcasting;
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.give === "broadcast") {
        this.broadcasting = "";
        callback();
      } else if (this.give === "broadcasting" && value === "") {
        callback(new Error(""));
      } else if (!PriceRegex.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const broadcast = (rule, value, callback) => {
      //--
      value = this.broadcast;
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.give === "broadcasting") {
        this.broadcast = "";
        callback();
      }
      if (this.give === "broadcast" && value === "") {
        callback(new Error(""));
      } else if (!PriceRegex.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      uploadIcon,
      crumbs: [
        { name: "", route: "/SetMealList?from=MainBuyManagement" },
        { name: "", route: "" }
      ],
      total: 0, // 
      display: 20, // 
      current: 1, // 
      allClass: true, //
      Appoint: false, //
      fication: false, //
      allClass1: true, //
      Appoint1: false, //
      fication1: false, //
      isShowDelModal: false, //
      isShowDelModall: false, //
      isShowDel: false, //
      isShowDell: false, //
      edit: false, //
      totall: 0, // 
      displayl: 20, // 
      currentl: 1, // 
      edit1: false, // 
      bradShow: false,
      activeIdex: 1, //   
      activePlus: 1, //    
      upload: [], //
      cheecck: [], //     
      cheeecck: [], //      
      valueTime: [
        `${new Date().getFullYear()}-${new Date().getMonth() +
          1}-${new Date().getDate()}`,
        `${new Date().getFullYear() + 1}-${new Date().getMonth() +
          1}-${new Date().getDate()}`
      ], //['2018-01-01', '2020-02-15']
      timeValue: [
        `${new Date().getFullYear()}-${new Date().getMonth() +
          1}-${new Date().getDate()}`,
        `${new Date().getFullYear() + 1}-${new Date().getMonth() +
          1}-${new Date().getDate()}`
      ], //['2018-01-01', '2020-02-15']
      radipurchaseo: "male", //radio
      threshold: "male", //radio
      term: "female", //
      termPlus: "female", // 
      give: "broadcast", //
      thresholdPlus: "male", //radio
      broadcast: "", //
      broadcasting: "", //
      check: "female", //
      starTime: "", // 
      endTime: "", // 
      starTimePlus: "", // 
      endTimePlus: "", // 
      formdate: "",
      //  classtypess: "1",
      checkFalse: "", // id
      checkTrue: "", // id 
      strage: "", //strage
      stragePlus: "", //strage
      treeData: [], //
      treeDataPlus: [], //
      data1: [], //
      data2: [], //
      dataM: [], //
      dataw: [], //
      categoryids: "", //ID
      categoryidsPlus: "", //ID 
      curriculumids: "", //id
      curriculumidsPlus: "", //id
      editTree: "", //
      editTreePlus: "", // 
      classifyNum: 0, //
      classifyNum1: 0, //
      optionss: {
        //
        disabledDate(date) {
          return date && date.valueOf() < Date.now() - 86400000;
        }
      },
      formItem: {
        classTime: "", //
        setName: "", //
        images: "", //url
        packagePrice: "", //
        classNum: "", //
        classPrice: "", //
        numDays: "", //
        tree: "", //
        treePlus: "", // 
        Daysnum: "", // 
        summary: "", //
        classPrice1: "" // input
      },
      ruleCustom: {
        // images: [{ required: true, validator: images,trigger: 'change' }],//
        classTime: [{ required: true, validator: classMeal,trigger: 'blur'}], //
        setName: [{ required: true, message: "",trigger: 'blur'}], //
        broadcasting: [{ required: true, validator: broadcasting,trigger: 'blur'}], //--
        broadcast: [{ required: true, validator: broadcast,trigger: 'blur'}], //--
        packagePrice: [{ required: true, validator: PriceMeal,trigger: 'blur'}], //
        classNum: [{ required: true, validator: classNum,trigger: 'blur'}], //
        classPrice: [{ required: true, validator: mealPrice,trigger: 'blur'}], //
        classPrice1: [{ required: true, validator: classPrice,trigger: 'blur'}], //
        numDays: [{ required: true, validator: numDays,trigger: 'blur'}], //
        Daysnum: [{ required: true, validator: Daysnum,trigger: 'blur'}], //
        // treeData: [{ required: true, validator: treeData }], //
        // treeDataPlus: [{ required: true, validator: treeDataPlus }],//
        summary: [{ required: true, message: "",trigger: 'blur'}] // 
      },
      store: 0, //
      storePlus: 0, //

      selectedCourse: [], //

      classifyIds: "",
      maleBol:true,//
      maleBol1:true,
      maleBol2:true,
      maleBol3:true,
      maleBol4:true,
      maleBol5:false,
      maleBol6:true,
      maleBol7:true,
    };
  },
  created() {
    this.upload = [3, 1, 1]; //
    let routeId = this.$route.query.id;
    this.getdate(routeId);
  },
  
  methods: {
    getdate(id) {
      //
      Server.post(Path.GetmealList, { setid: id }, res => {
        let { code, data } = res;
        if (code === 0) {
          let datas = res.data.bug;
          let datam = res.data.give;
          this.formItem.classTime = datas[0].bughour; //
          this.formItem.setName = datas[0].setmeal; //
          this.formItem.images = datas[0].setimgpath; //
          this.formItem.summary = datas[0].content; //
          // this.formItem.starTime = datas[0].effstarttime; //
          // this.formItem.endTime = datas[0].effendtime; //
          this.formItem.packagePrice = datas[0].setprice; //

          if (datas[0].limitbuy === 0) {
            //
            this.radipurchaseo = "male"; //
          } else {
            this.radipurchaseo = "female";
            this.formItem.classNum = datas[0].limitbuy;
          }
          if (datas[0].threshold === 0) {
            //
            this.threshold = "male"; //
          } else {
            //
            this.threshold = "female";
            this.formItem.classPrice = datas[0].threshold;
          }
          if (datas[0].efftype === 1) {
            //
            //
            this.term = "female";
            this.valueTime = [];
            this.valueTime[0] = this.stamp(datas[0].effstarttime);
            this.valueTime[1] = this.stamp(datas[0].effendtime);
          } else {
            //
            this.term = "days";
            this.formItem.numDays = datas[0].efftime; //
          }
          //
          this.editTree = datas[0].categoryids;
          if (datas[0].trialtype === 2) {
            //
            this.active("Appoint");
            this.isShowDelModal = false;
            this.edit = true;
            this.store = 2;
            localStorage.setItem('categoryids', datas[0].categoryids);
            let arr = [];
            if (datas[0].categoryids !== "") {
              arr = datas[0].categoryids.split(",").map(item => {
                return Number(item);
              });
            }
            this.classifyNum = arr.length;
          } else if (datas[0].trialtype === 3) {
            //
            this.active("fication");
            this.isShowDel = false;
            if (datas[0].curriculumids !== "") {
              this.checkFalse = datas[0].curriculumids;
              window.localStorage.setItem("lv", datas[0].curriculumids);
            }

            this.edit = true;
            this.store = 3;
            // this.strage = datas[0].curriculumids.split(",");
          }
          //
          if (datas[0].givestatus === 1) {
            // 
            this.check = "male";
            if (res.data.give && datam[0]) {
              if (datam[0].giftefftype === 1) {
                //
                this.term = "female";
                this.timeValue = [];
                this.timeValue[0] = this.stamp(datam[0].gifteffstarttime);
                this.timeValue[1] = this.stamp(datam[0].gifteffendtime);
              } else {
                //
                this.termPlus = "days";
                this.formItem.Daysnum = datam[0].giftefftime;
              }

              if (datam[0].sendvideo === 0) {
                //
                this.broadcasting = datam[0].sendlive;
                this.give = "broadcasting";
              } else {
                //
                this.broadcast = datam[0].sendvideo;
                this.give = "broadcast";
              }
              //
              this.editTreePlus = datam[0].giftcategoryids;
              if (datam[0].gifttrialtype === 2) {
                //
                this.active1("Appoint1");
                this.isShowDelModall = false;
                this.edit1 = true;
                this.storePlus = 2;
                localStorage.setItem('categoryidsPlus', datam[0].giftcategoryids);
                let arr = [];

                if (datam[0].giftcategoryids !== "") {
                  arr = datam[0].giftcategoryids.split(",");
                  arr = arr.map(item => {
                    return Number(item);
                  });
                }
                this.classifyNum1 = arr.length;
              } else if (datam[0].gifttrialtype === 3) {
                //
                this.active1("fication1");
                this.isShowDell = false;
                this.checkTrue = datam[0].giftcurriculumids;
                if (datam[0].giftcurriculumids !== "") {
                  this.checkTrue = datam[0].giftcurriculumids;
                  window.localStorage.setItem(
                    "lvv",
                    datam[0].giftcurriculumids
                  );
                }
                this.edit1 = true;
                this.storePlus = 3;
                this.stragePlus = (datam[0].giftcurriculumids||"").split(",");
              }
              if (datam[0].giftthreshold === 0) {
                this.thresholdPlus = "male";
              } else {
                this.thresholdPlus = "female";
                this.formItem.classPrice1 = datam[0].giftthreshold;
              }
            }
          } else {
            this.check = "female";
          } //
        } else if (code === 60016) {
          this.$Message.error(res.info);
        } else {
          this.$Message.error(res.info);
        }
      });
    },

    setTree() {
      //
      this.treeData = this.$refs.mTree.getCheckedNodes();
      let arrId = [];
      for (let i = 0; i < this.treeData.length; i++) {
        arrId.push(this.treeData[i].id);
      }
      this.categoryids = arrId.join();
      localStorage.setItem('categoryids', this.categoryids);
      this.classifyNum = arrId.length;
      this.edit=true;
    },

    setTreePlus() {
      // 
      this.treeDataPlus = this.$refs.mTreePlus.getCheckedNodes();
      let arrId = [];
      for (let i = 0; i < this.treeDataPlus.length; i++) {
        arrId.push(this.treeDataPlus[i].id);
      }
      this.categoryidsPlus = arrId.join();
      localStorage.setItem('categoryidsPlus', this.categoryidsPlus);
      this.classifyNum1 = arrId.length;
      this.edit1=true;
    },
    treeModale(Choice) {
      // 
      Choice === "main"
        ? (this.isShowDelModal = true)
        : (this.isShowDelModall = true);
      Server.post(
        Path.getEditree,
        {
          categoryids: Choice === "main" ? this.editTree : this.editTreePlus
        },
        res => {
          let { code, data } = res;
          this.data1 = [];
          this.data2 = [];
          for (let i = 0; i < res.data.length; i++) {
            let data = {};
            data.title = res.data[i].title;
            data.id = res.data[i].id;
            data.fatherid = res.data[i].fatherid;
            data.children = res.data[i].children;
            data.checked = res.data[i].checked;
            Choice === "main" ? this.data1.push(data) : this.data2.push(data);
          }
        }
      );
    },
    moisShow(items) {
      //
      if (items === "mian") {
        this.isShowDelModal = false;
        this.treeData = [];
      } else {
        this.isShowDelModall = false;
        this.treeDataPlus = [];
      }
    },
    handleSubmit(name) {
      //
      this.$refs[name].validate(valid => {
        if (valid) {
          this.SaveData();
        }
      });
    },

    //
    SaveData() {
      //
      if (this.formItem.images === "") {
        this.$Message.error("");
        return false;
      }
      if (this.term === "female" && this.starTime === "") {
        this.handleChange(this.valueTime);
      }
      if (this.termPlus === "female" && this.starTimePlus === "") {
        this.handleChangePlus(this.timeValue);
      }

      if(localStorage.getItem('categoryids') !== this.categoryids) {
        
        this.categoryids = localStorage.getItem('categoryids');
      }
      if(localStorage.getItem('categoryidsPlus') !== this.categoryidsPlus) {
        
        this.categoryidsPlus = localStorage.getItem('categoryidsPlus');
      }

      Server.post(
        Path.getListFrom,
        {
          id: this.$route.query.id,
          bughour: this.formItem.classTime, //
          setmeal: this.formItem.setName, //
          setprice: this.formItem.packagePrice, //
          limitbuy:
            this.radipurchaseo === "male" ? "0" : this.formItem.classNum, //(0: :)
          threshold: this.threshold === "male" ? 0 : this.formItem.classPrice, //(0: :)
          efftype: {
            status: this.term === "female" ? 1 : 2, //1 2",
            effstarttime: this.starTime, //()
            effendtime: this.endTime, //()",
            efftime: this.formItem.numDays //"
          },
          trialtype: {
            status: this.activeIdex, //123
            categoryids: this.categoryids, //"ids"
            curriculumids: this.curriculumids //ids"
          },
          content: this.formItem.summary, //",
          setimgpath: this.formItem.images, //"",
          givestatus: this.check === "male" ? 1 : 0, // "0 1",
          sendvideo: this.broadcast, //"",
          sendlive: this.broadcasting, //"",
          giftthreshold:
            this.thresholdPlus == "male" ? 0 : this.formItem.classPrice1, //"(0: :)",
          giftefftype: {
            status: this.termPlus === "female" ? 1 : 2, //1 2,
            gifteffstarttime: this.starTimePlus, //"()",
            gifteffendtime: this.endTimePlus, //"()",
            giftefftime: this.formItem.Daysnum //""
          },
          gifttrialtype: {
            status: this.activePlus, //"123",
            giftcategoryids: this.categoryidsPlus, //"ids",
            giftcurriculumids: this.curriculumidsPlus //"ids"
          }
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.$router.go(-1);
            this.$Message.success(res.info);
            localStorage.removeItem('categoryids');
            localStorage.removeItem('categoryidsPlus');
            localStorage.removeItem('lv');
            localStorage.removeItem('lvv');
          } else {
            this.$Message.error(res.info);
          }
        }
      );
    },
    stamp(times) {//
      let date = new Date(times * 1000);
      let Y = date.getFullYear() + '-';
      let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
      let D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
      let h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
      let m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
      let s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
      return Y+M+D+h+m+s;
    },
    getDate(value) {
      //
      this.strage = value;
      localStorage.lv = value;
      this.curriculumids = value.join();
    },
    getDatePlus(value) {
      //
      this.stragePlus = value;
      localStorage.lvv = value;
      this.curriculumidsPlus = value.join();
    },
    active(mun, num) {
      if (localStorage.getItem("store") != num) {
        this.store = num;
        localStorage.setItem("store", this.store);
      }
      if (mun == "allClass") {
        //
        this.allClass = true;
        this.Appoint = false;
        this.fication = false;
        this.edit = false;
        this.activeIdex = 1;
      }
      if (mun == "Appoint") {
        //
        this.allClass = false;
        this.Appoint = true;
        this.fication = false;
        this.data1.length == 0
          ? this.treeModale("main")
          : (this.isShowDelModal = true);
        // this.treeModale("main");
        // this.edit = false;
        this.activeIdex = 2;
      }
      if (mun == "fication") {
        //
        this.allClass = false;
        this.Appoint = false;
        this.fication = true;
        this.classModale(null, "mian");
        // this.edit = false;
        this.activeIdex = 3;
      }
    },
    handleChange(items) {
      //
      this.starTime = items[0].split("-");
      this.endTime = items[1].split("-");
      this.starTime = new Date(this.starTime).getTime() / 1000;
      this.endTime = new Date(this.endTime).getTime() / 1000;
    },
    handleChangePlus(items) {
      //
      this.starTimePlus = items[0].split("-");
      this.endTimePlus = items[1].split("-");
      this.starTimePlus = new Date(this.starTimePlus).getTime() / 1000;
      this.endTimePlus = new Date(this.endTimePlus).getTime() / 1000;
    },
    pagechange(currentPage) {
      //
      this.current = currentPage;
      this.loading = true;
      this.classModale(this.current, "mian");
    },
    pagechangePlus(currentPage) {
      //
      this.currentl = currentPage;
      this.loading = true;
      this.classModale(this.currentl, "plus");
    },

    /**
     * 
     */
    selectModalCourse(arr) {
      let curriculumids=[];
      if (this.checkFalse=="") {
        curriculumids=[];
      } else {
        curriculumids = this.checkFalse.split(",");
      }
      arr.forEach(el => {
        curriculumids.forEach(item => {
          if (el.id == item) {
            this.$set(el, "ischeck", true);
          }
        });
      });
    },
    classModale(page = 1, occ) {
      // 
      occ === "mian" ? (this.isShowDel = true) : (this.isShowDell = true); //
      let classtypess = "";
      if (occ != "mian") {
        //
        this.give == "broadcasting" ? (classtypess = "2") : (classtypess = "1"); //
      } else {
        classtypess = "";
      }
      Server.post(
        Path.getClassList,
        {
          curriculumids: occ === "mian" ? this.checkFalse : this.checkTrue,
          coursename: "",
          classtypes: classtypess,
          pagenum: page
        },
        res => {
          let { code, data } = res;
          this.dataM = [];
          this.dataw = [];

          if (code == 0) {
            if (occ === "mian") {
              this.total = res.data.pageinfo.total;
              this.display = Number(res.data.pageinfo.pagesize);
              this.cheecck = [];
              let checkFalse = [];
              if (this.checkFalse=="") {
                checkFalse = [];
              } else {
                checkFalse = this.checkFalse.split(",");
              }
              for (var p = 0; p < checkFalse.length; p++) {
                this.cheecck.push(Number(checkFalse[p]));
              }
              
            } else {
              this.totall = res.data.pageinfo.total;
              this.displayl = Number(res.data.pageinfo.pagesize);
              this.cheeecck = [];
              let checkTrue = [];
              if (this.checkTrue=="") {
                checkTrue = [];
              } else {
                checkTrue = this.checkTrue.split(",");
              }
              for (var p = 0; p < checkTrue.length; p++) {
                this.cheeecck.push(Number(checkTrue[p]));
              }
            }
            for (let i in res.data.curriculumlist) {
              let data = {};
              data.id = res.data.curriculumlist[i].id;
              data.coursename = res.data.curriculumlist[i].coursename;
              data.imageurl = res.data.curriculumlist[i].imageurl;
              //res.data.curriculumlist[i].checked === 1 ?  data.focus = true : data.focus = false;
              res.data.curriculumlist[i].checked === 1
                ? (data.focus = true)
                : (data.focus = false);
              //data.focus = false
              occ === "mian" ? this.dataM.push(data) : this.dataw.push(data);
            }
            if (occ === "mian") {
              for (var i = 0; i < this.dataM.length; i++) {
                // id
                for (var j = 0; j < this.strage.length; j++) {
                  if (this.dataM[i].id == this.strage[j]) {
                    this.dataM[i].focus = true;
                  }
                }
              }
            } else {
              for (var i = 0; i < this.dataw.length; i++) {
                // id 
                for (var j = 0; j < this.stragePlus.length; j++) {
                  if (this.dataw[i].id == this.stragePlus[j]) {
                    this.dataw[i].focus = true;
                  }
                }
              }
            }
          } else {
            this.$Message.error(res.info);
          }
          this.selectedCourse = data.curriculumlist; //
          this.selectModalCourse(this.selectedCourse);
          let localSelectCourseIds = localStorage.getItem("lv").split(",");
          localSelectCourseIds = localSelectCourseIds.map(item => {
            return parseInt(item);
          });
          // this.getDate(this.cheecck);
          this.cheecck = localSelectCourseIds;
          this.getDate(localSelectCourseIds);

          let localSelectCoursezIds = localStorage.getItem("lvv").split(",");
          localSelectCoursezIds = localSelectCoursezIds.map(item => {
            return parseInt(item);
          });
          this.cheeecck = localSelectCoursezIds;
          this.getDatePlus(localSelectCoursezIds);
        }
      );
    },
    backRou() {
      this.$router.go(-1);
    },
    active1(mun, num) {
      if (localStorage.getItem("storePlus") != num) {
        this.storePlus = num;
        localStorage.setItem("storePlus", this.storePlus);
      }
      if (mun == "allClass1") {
        //
        this.allClass1 = true;
        this.Appoint1 = false;
        this.fication1 = false;
        this.edit1 = false;
        this.activePlus = 1;
      }
      if (mun == "Appoint1") {
        //
        this.allClass1 = false;
        this.Appoint1 = true;
        this.fication1 = false;
        this.data2.length == 0
          ? this.treeModale("plus")
          : (this.isShowDelModall = true);
        // this.treeModale("plus");
        // this.edit1 = false;
        this.activePlus = 2;
      }
      if (mun == "fication1") {
        //
        this.allClass1 = false;
        this.Appoint1 = false;
        this.fication1 = true;
        this.classModale(null, "plus");
        // this.edit1 = false;
        this.activePlus = 3;
      }
    },
    getLogoImg(datas) {
      //url
      this.formItem.images = datas;
    }
  },
  watch: {
    radipurchaseo(val){
      val=='male'?this.maleBol=false:this.maleBol=true
    },
    threshold(val){
      val=='male'?this.maleBol1=false:this.maleBol1=true
    },
    term(val){
      val=='female'?this.maleBol2=false:this.maleBol2=true
    },
    give(val){
      if (val=='broadcast') {
        this.maleBol5=false;
        this.maleBol4=true
      } else {
        this.maleBol4=false;
        this.maleBol5=true
      }
    },
    thresholdPlus(val){
      val=='male'?this.maleBol6=false:this.maleBol6=true
    },
    termPlus(val){
      val=='female'?this.maleBol7=false:this.maleBol7=true
    },
    //
    activeIdex(news) {
      
      if (news == 1) {
        this.data1 = [];
        this.treeData = [];
        this.categoryids = "";
        this.strage = "";
        // localStorage.lv = "";
        this.curriculumids = "";
      } else if (news == 2) {
        this.strage = "";
        // localStorage.lv = "";
        this.curriculumids = "";
      } else {
        this.data1 = [];
        this.treeData = [];
        this.categoryids = "";
      }
    },
    //
    activePlus(news) {
      
      if (news == 1) {
        this.data2 = [];
        this.treeDataPlus = [];
        this.categoryidsPlus = "";
        this.stragePlus = "";
        // localStorage.lvv = "";
        this.curriculumidsPlus = "";
      } else if (news == 2) {
        this.stragePlus = "";
        // localStorage.lvv = "";
        this.curriculumidsPlus = "";
      } else {
        this.data2 = [];
        this.treeDataPlus = [];
        this.categoryidsPlus = "";
      }
    },
  }
};
</script>
<style scoped>
/* @import "~@/assets/common.css"; */
.mealTime {
  width: 10%;
  margin: 0 5px;
}
.cardP0 .ivu-card-body {
  padding: 0;
}
.term {
  position: relative;
}
.checkboxGroup {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-left: 15px;
  /*justify-content: space-between;*/
}
.claasLi {
  width: 19%;
  margin-bottom: 20px;
  height: 130px;
  border: 1px solid #cfdce7;
  border-radius: 4px;
  position: relative;
  margin-left: 5px;
}
.claasLi img {
  border: none;
  margin-left: 7%;
  margin-top: 4%;
  border-radius: 4px;
  width: 86%;
  height: 76%;
}
.classBox {
  position: absolute;
  left: 7%;
  top: 80%;
  width: 134px;
  height: 30px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.classSpan {
  position: absolute;
  left: 7%;
  top: 80%;
  font-size: 12px;
}
.terminpt {
  width: 17%;
  position: absolute;
  top: 55px;
  left: 80px;
}
.classBtn {
  color: #515a6e;
  background-color: #fff;
  border-color: #cccccc;
  margin-right: 20px;
}
.btnActive {
  /* background: linear-gradient(left, #387bff, #22b2ee); */
  background: #3f9ce8;
  color: #ffffff;
}
/* .mgSelect {
  width: 90%;
  border: 1px solid #cfdce7;
  border-radius: 4px;
} */
.btn {
  width: 120px;
  line-height: 40px;
  text-align: center;
  border-radius: 40px;
  cursor: pointer;
  margin: 20px 0;
  border: none;
}
.btn:hover {
  color: none;
}
.pfication {
  width: 50%;
  height: 100%;
}
.classification {
  margin-left: 5px;
  line-height: 40px;
}

.edit {
  color: #387bff;
  cursor: pointer;
}
/* .ivu-modal-body {
  height: 500px;
  overflow: auto;
} */
.logo-box {
  height: 68px;
  display: block;
  /*background:rgba(238,246,245,1) url('./images/icon_upload.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size:40px; */
  /* border: 1px solid #cfdce7; */
  border-radius: 4px;
  cursor: pointer;
  width: 206px;
}
.logo-box {
  height: 100%;
}
.logo {
  /* width: 204px; */
  height: 68px;
  display: block;
  margin: 0 auto;
}
/* .borR {
  border: none;
  border: 1px solid #cfdce7;
} */

.classTime {
  color: #ed3f14;
  z-index: 888;
  position: absolute;
}
</style>



