<template>
  <div>
    <panel-title :crumbs="pathArr" />
     <Card dis-hover class="mytheme-pd-left-20 mytheme-pd-right-20 mytheme-mg-bottom-30">
      <div slot="title" class="head">
        <img :src="fillIn">
        <span></span>
      </div>
         <Form>
            <Row>
                <Col>
                    <FormItem label="">
                        <Select v-model="courseId" filterable :disabled='notSelect' @on-change="getPeriodList(courseId)" placeholder="" style="width:300px;border: 1px solid #dddee1;border-radius: 4px;">
                            <Option v-for="item in classifyOne" :value="item.id" :key="item.id">{{item.coursename}}</Option>
                        </Select>
                    </FormItem>
                </Col>
            </Row>
            <Row>
                <Col>
                    <FormItem label="">
                        <Select v-model="periodId" filterable :disabled='notSelect' placeholder=""  @on-change="checkHaveExercise()" style="width:300px;border: 1px solid #dddee1;border-radius: 4px;">
                            <Option v-for="item in classifyTow" :value="item.id" :key="item.id">{{item.periodname}}</Option>
                        </Select>
                    </FormItem>
                </Col>
            </Row>
            <!-- <Row v-if="$route.params.enterType==2">
                <Col>
                    <FormItem>
                        <h3 style="width:870px;text-align: center;">{{choiceQues.periodname}}</h3>
                    </FormItem>
                </Col>
            </Row> -->
            <Row>
                <Col>
                    <FormItem>
                        <!-- :disabled="courseId=='' || periodId==''" -->
                        <Button type="warning" :disabled='replaceBol' @click="initAddBut()"></Button>
                    </FormItem>
                </Col>
            </Row>
            <Row>
            <Col>
                <FormItem label=""> </FormItem>
                    <section class="homework mytheme-mg-bottom-30">
                        <div v-for='(item,k,index) in getQuesNum(choiceQues.subject)' :key="item.index">
                            <h4>
                                <span>{{index=='0'?'':(index=='1'?'':(index=='2'?'':''))}}</span>
                                <span>{{k=='1'?'':(k=='2'?'':(k=='3'?'':''))}}</span>
                                <span>({{totalScore(item)}})</span>
                            </h4>
                            <dl v-for='(i,indexs) in item' :key="i.indexs">
                                <dt style="width:586px;word-break:break-all;">{{indexs+1}}{{i.name}}</dt>
                                <dd  v-if="i.imageurl" >
                                     <img :src="'http://'+img" v-for="img in transfer(i.imageurl)" :key="img.id" style="width:150px;height:100px;margin-right:15px;border:solid 1px #eee;" />
                                </dd>
                                <dd v-if="k==1||k==2">
                                    <p v-for="option in i.options" :key="option.id">{{option}}</p>
                                </dd>
                                <!--<dd v-if="i.analysis">{{i.analysis}}</dd>-->
                                <dd>
                                    <span v-if="i.correctanswer" style="word-break:break-all;">{{i.correctanswer}}</span>
                                    <span v-if="i.analysis" style="word-break:break-all;">{{i.analysis}}</span>
                                </dd>
                                <dd class="delStyle">
                                    <span @click="showEditModal(indexs,k,i)"></span>
                                    <span @click="showDelModal(indexs,k)"></span>
                                </dd>
                            </dl>
                        </div>
                    </section>
            </Col>
            </Row>
             <Row style="width:870px;text-align: center;">
                <Col>
                    <!--  -->
                    <Button type="primary"  @click="addExercise()"></Button>
                    <!--  -->
                    <Button @click="$router.push({path:'/QuestionBank',query:{from:'TeachingAffairs'}})"></Button>
                </Col>
            </Row>
        </Form>
      </Card>
       <!---->
      <Modal
            v-model="hintModel"
            :closable="false">
        <div slot="header"></div>
        <div class="type-content">
           <p></p>
        </div>
        <div slot="footer">
            <!---->
            <Button type="primary" @click="delSubject()"></Button>
            <!---->
            <Button @click="hintModel = false;"></Button>
        </div>
    </Modal>
   <!---->
    <Modal
        width="715"
        :closable="false"
        v-model="addQuesModel">
        <div slot="header">{{addEditText}}</div>
        <div style="padding:0 120px;">
            <Row class="mytheme-pd-bottom-20 ">
                <Col>
                    <RadioGroup v-model="quesType" class="radioGroup">
                        <Radio :disabled="changeType" v-for="i in radios" :label='i.label' :key='i.lable'>{{i.value}}</Radio>
                    </RadioGroup>
                </Col>
            </Row>
            <Form ref="topic" :model="topic" :rules="ruleValidate" :label-width="110">
            <!-- -->
                <Row>
                    <Col>
                        <FormItem prop="stem" label="">
                            <Input v-model.trim="topic.stem" placeholder=",100" :maxlength="100" style="width: 288px;"/>
                        </FormItem>
                    </Col>
                </Row>
            <!---->
                <Row>
                    <Col>
                        <FormItem label="">
                            <div style="display:flex;">
                                <!--  -->
                                <div v-if="topic.imagesArr[0]!=''" class="demo-upload-list" v-for="(item,index) in topic.imagesArr" :key="index">
                                    <img  :src="'http://'+item">
                                    <div class="demo-upload-list-cover">
                                        <Icon type="ios-eye-outline" @click.native="handleView(item)"></Icon>
                                        <Icon type="ios-trash-outline" @click.native="handleRemove(index)"></Icon>
                                    </div>
                                </div>
                                <!-- -->
                                <Upload
                                    v-if="topic.imagesArr.length<4"
                                    :action="imgDate"
                                    ref="upload"
                                    name="uploadFile"
                                    :data="{allpathnode:'11,1,1',filetype:'1'}"
                                    :show-upload-list="false"
                                    :on-success="handleSuccess"
                                    >
                                    <div class="demo-upload-list">
                                        <img class="photo" src="./assets/images/uploadIcon@2x.png" alt="">
                                    </div>
                                </Upload>
                            </div>
                            <!--  -->
                            <Modal v-model="visible" style="position:fixed;z-index:2000;">
                                <div slot="header"></div>
                                <img :src="'http://'+topic.imgName" v-if="visible" style="width: 100%">
                                 <div slot="footer">
                                    <!---->
                                    <!-- <Button type="primary" @click="visible=false"></Button> -->
                                    <!---->
                                    <!-- <Button @click="visible=false"></Button> -->
                                </div>
                            </Modal>
                        </FormItem>
                    </Col>
                </Row>
                <!-- -->
                <Row  v-if="quesType=='1' || quesType=='2'">
                    <Col>
                        <FormItem label=""></FormItem>
                        <div>
                            <Row>
                                <Col>
                                    <FormItem prop="optionA" label="A">
                                        <Input v-model.trim="topic.optionA" placeholder="A,50" :maxlength="50" style="width: 288px;"/>
                                    </FormItem>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <FormItem prop="optionB" label="B">
                                        <Input v-model.trim="topic.optionB" placeholder="B,50" :maxlength="50" style="width: 288px;"/>
                                    </FormItem>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <FormItem prop="optionC" label="C">
                                        <Input v-model.trim="topic.optionC" placeholder="C,50" :maxlength="50" style="width: 288px;"/>
                                    </FormItem>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <FormItem prop="optionD" label="D">
                                        <Input v-model.trim="topic.optionD" placeholder="D,50" :maxlength="50" style="width: 288px;"/>
                                    </FormItem>
                                </Col>
                            </Row>
                        </div>
                    </Col>
                </Row>
                <!-- -->
                <Row v-if="quesType =='1'">
                    <Col>
                        <FormItem prop="" label="">
                            <RadioGroup v-model="topic.answer" class="radioGroup">
                                <Radio v-for="i in radioOpt" :label='i' :key='i'>{{i}}</Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                </Row>
                <!-- -->
                <Row v-if="quesType =='2'">
                    <Col>
                        <FormItem prop="" label="">
                            <CheckboxGroup class="radioGroup" v-model='multiSelect' @on-change="handleCheck">
                                <Checkbox  v-for="(item,index) in radioCheck" :key="index"  :label="item.label">
                                    {{item.name}}
                                </Checkbox>
                            </CheckboxGroup>
                        </FormItem>
                    </Col>
                </Row>
                <!-- -->
                <Row v-if="quesType =='3'">
                    <Col>
                        <FormItem prop="answer" label="">
                            <!-- <span v-if="quesType ==1||2">{{topic.answer}}</span>  -->
                            <Input v-model.trim="topic.answer" placeholder=",100" :maxlength="100" style="width: 288px;"/>
                        </FormItem>
                    </Col>
                </Row>
                <!-- -->
                <Row v-if="quesType =='4'">
                    <Col>
                        <FormItem prop="analytic" label="">
                            <Input v-model.trim="topic.analytic" type="textarea" :rows="5" placeholder=",300" :maxlength="300" style="width: 288px;resize:none" />
                        </FormItem>
                    </Col>
                </Row>
                <!-- -->
                <Row>
                    <Col>
                        <FormItem prop="score" label="">
                            <!-- onkeyup="this.value=this.value.replace(/\D/g,'')" onafterpaste="this.value=this.value.replace(/\D/g,'')" -->
                            <Input v-model.trim="topic.score" :maxlength="3" placeholder="" style="width: 210px;margin-right:10px;"/>
                        </FormItem>
                    </Col>
                </Row>
            </Form>
        </div>
        <div slot="footer">
            <!---->
            <Button type="primary" @click="saveSubject('topic')"></Button>
            <!---->
            <Button @click="handleReset('topic')"></Button>
        </div>
  </Modal>
  </div>
</template>
<script>
import {
  Card,
  Col,
  Input,
  Select,
  Option,
  Button,
  Modal,
  Form,
  FormItem,
  Row,
  RadioGroup,
  Radio,
  Upload,
  Icon,
  CheckboxGroup,
  Checkbox
} from "iview";
import panelTitle from "@/components/panelTitle";
import fillIn from "./assets/images/fillIn@2x.png";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "editQues",
  components: {
    panelTitle,
    Card,
    Col,
    Input,
    Select,
    Option,
    Button,
    Modal,
    Form,
    FormItem,
    Row,
    RadioGroup,
    Radio,
    Upload,
    Icon,
    CheckboxGroup,
    Checkbox
  },
  data() {
    const emptyRegex = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      }
      callback();
    };
    const optionRegex = (rule, value, callback) => {
      if (this.quesType == 1 || this.quesType == 2) {
        if (value === "") {
          callback(new Error(""));
        }
      }
      callback();
    };
    const analyRegex = (rule, value, callback) => {
      if (this.quesType == 4) {
        if (value === "") {
          callback(new Error(""));
        }
      }
      callback();
    };
    const ScoreRegex = (rule, value, callback) => {
      let scoreRegex = /^100$|^([1-9]|[1-9]\d)$/;
      // let scoreRegex = /^[1-9]\d*$/;
      if (value === "") {
        callback(new Error(""));
      } else if (!scoreRegex.test(value)) {
        callback(new Error("0100"));
      } else {
        callback();
      }
    };
    return {
      fillIn,
      imgDate:
        window.location.origin === "http://127.0.0.1:8080" ||
        window.location.origin === "http://127.0.0.1:8081"
          ? "http://www.lemonedu.com/teacher/Uploadfile/Upload"
          : "/teacher/Uploadfile/Upload",
      pathArr: [], //
      classifyOne: [], //
      courseId: "", //ID
      classifyTow: [], //
      periodId: "", //ID
      notSelect: false, //
      replaceBol: true, //
      addQuesModel: false, //
      quesType: "1", //
      topic: {
        stem: "", //
        imagesArr: [], //
        imgName: "", //
        optionA: "", //
        optionB: "",
        optionC: "",
        optionD: "",
        answer: "", //
        analytic: "", //
        score: "" //
      },
      visible: false, //
      ruleValidate: {
        stem: [{ required: true, validator: emptyRegex, trigger: "blur" }],
        optionA: [{ required: true, validator: optionRegex, trigger: "blur" }],
        optionB: [{ required: true, validator: optionRegex, trigger: "blur" }],
        optionC: [{ required: true, validator: optionRegex, trigger: "blur" }],
        optionD: [{ required: true, validator: optionRegex, trigger: "blur" }],
        answer: [{ required: true, validator: emptyRegex, trigger: "blur" }],
        analytic: [{ required: true, validator: analyRegex, trigger: "blur" }],
        score: [{ required: true, validator: ScoreRegex, trigger: "blur" }]
      },
      choiceQues: {
        //
        courseid: "",
        periodid: "",
        periodname: "",
        subject: { "1": [], "2": [], "3": [], "4": [] }
      },
      single: [], //
      multiple: [], //
      gapFilling: [], //
      composition: [], //
      radios: [
        {
          value: "",
          label: "1"
        },
        {
          value: "",
          label: "2"
        },
        {
          value: "",
          label: "3"
        },
        {
          value: "",
          label: "4"
        }
      ],
      radioOpt: ["A", "B", "C", "D"], //
      radioCheck: [
        { label: "A", name: "A" },
        { label: "B", name: " B" },
        { label: "C", name: "C" },
        { label: "D", name: "D" }
      ], //
      saveAnswer: [], //
      hintModel: false, //
      delIndex: "",
      delKey: "",
      isHandle: "add",
      addEditText: "",
      multiSelect: [],
      changeType: false
    };
  },
  methods: {
    transfer(img) {
      //
      return img.split(",");
    },
    //
    initAddBut() {
      if (this.courseId == "" || this.periodId == "") {
        this.$Message.error(""); //
      } else {
        this.addQuesModel = true;
        this.isHandle = "add";
        this.$refs["topic"].resetFields();
      }

      this.quesType = "1";
      this.topic = {
        stem: "", //
        imagesArr: [], //
        imgName: "", //
        optionA: "", //
        optionB: "",
        optionC: "",
        optionD: "",
        answer: "", //
        analytic: "", //
        score: "" //
      };
    },
    //
    getAllCurriculum() {
      Server.post(
        Path.getAllCurriculum,
        {
          coursename: ""
        },
        res => {
          if (res.code === 0) {
            this.classifyOne = res.data;
          }
        }
      );
    },
    //
    getPeriodList(id) {
      Server.post(
        Path.getPeriodList,
        {
          curriculumid: id,
          periodname: ""
        },
        res => {
          if (res.code === 0) {
            this.classifyTow = res.data;
          }
        }
      );
    },
    //
    checkHaveExercise() {
      if (this.$route.params.enterType == 2) {
        return;
      }
      Server.post(
        Path.checkHaveExercise,
        {
          courseid: this.courseId,
          periodid: this.periodId
        },
        res => {
          if (res.code === 70412) {
            if (this.$route.params.enterType == 1) {
              this.$Message.error(res.info);
            }
            this.replaceBol = true;
          } else {
            this.replaceBol = false;
          }
        }
      );
    },
    handleCheck(nData) {
      //
      if (nData == null || nData == "") {
        this.topic.answer = "";
      } else {
        this.topic.answer = nData.join().toUpperCase();
      }
    },
    //is or  
    saveSubject(name) {
      this.changeType = false;
      this.$refs[name].validate(valid => {
        if (valid) {
          if (this.isHandle == "add") {
            this.addSubject();
          } else {
            this.editSubject();
          }
          this.addQuesModel = false;
        }
      });
      // this.$refs[name].resetFields();
    },
    handleReset(name) {
      this.changeType = false;
      //
      this.$refs[name].resetFields();
      this.addQuesModel = false;
      this.topic.analytic = "";
      this.multiSelect = [];
    },
    //
    addSubject() {
      let imgStr = this.topic.imagesArr.join(",");

      let item = {
        type: this.quesType,
        name: this.topic.stem,
        imageurl: imgStr,
        options: [
          "A." + this.topic.optionA,
          "B." + this.topic.optionB,
          "C." + this.topic.optionC,
          "D." + this.topic.optionD
        ],
        analysis: this.topic.analytic,
        correctanswer: this.topic.answer,
        score: this.topic.score
      };

      if (this.quesType == 1) {
        this.single.push(item);
      } else if (this.quesType == 2) {
        this.multiple.push(item);
      } else if (this.quesType == 3) {
        item.options = [];
        this.gapFilling.push(item);
      } else if (this.quesType == 4) {
        this.composition.push(item);
      }

      this.choiceQues.courseid = this.courseId;
      this.choiceQues.periodid = this.periodId;
      this.choiceQues.subject["1"] = this.single;
      this.choiceQues.subject["2"] = this.multiple;
      this.choiceQues.subject["3"] = this.gapFilling;
      this.choiceQues.subject["4"] = this.composition;
    },
    //
    getQuesNum(obj) {
      for (let k in obj) {
        if (obj[k].length == 0) {
          delete obj[k];
        }
      }
      return obj;
    },
    //
    totalScore(arr) {
      let sum = 0;
      arr.forEach(item => {
        sum += parseInt(item.score);
      });
      return sum;
    },
    //
    showEditModal(index, k, item) {
      //  this.getExerciseinfo()
      this.changeType = true;
      this.isHandle = "edit";

      this.addQuesModel = true;
      this.addEditText = "";
      this.delIndex = index;
      this.delKey = k;

      this.quesType = k;
      this.topic.stem = item.name;
      if (item.imageurl != null) {
        this.topic.imagesArr = item.imageurl.split(",");
      }
      // console.log(item.imageurl)
      if (item.options != "") {
        this.topic.optionA = item.options[0].substring(2);
        this.topic.optionB = item.options[1].substring(2);
        this.topic.optionC = item.options[2].substring(2);
        this.topic.optionD = item.options[3].substring(2);
      }
      this.topic.answer = item.correctanswer;
      this.multiSelect = this.transfer(item.correctanswer);
      this.topic.analytic = item.analysis;
      this.topic.score = item.score;
      // console.log(this.topic)
    },
    //
    editSubject() {
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].name = this.topic.stem;
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].name = this.topic.stem;
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].imageurl = this.topic.imagesArr.join(",");
      this.choiceQues.subject[this.delKey][this.delIndex].type = this.quesType;
      this.choiceQues.subject[this.delKey][this.delIndex].options = [
        "A." + this.topic.optionA,
        "B." + this.topic.optionB,
        "C." + this.topic.optionC,
        "D." + this.topic.optionD
      ];
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].correctanswer = this.topic.answer;
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].analysis = this.topic.analytic;
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].score = this.topic.score;
    },
    //
    showDelModal(index, k) {
      this.hintModel = true;
      this.delIndex = index;
      this.delKey = k;
    },
    //
    delSubject() {
      this.choiceQues.subject[this.delKey].splice(this.delIndex, 1);
      this.hintModel = false;
    },

    //
    handleView(name) {
      this.topic.imgName = name;
      this.visible = true;
    },
    // 
    handleRemove(index) {
      this.topic.imagesArr.splice(index, 1);
    },
    // 
    handleSuccess(res) {
      if (res.code == 0) {
        this.topic.imagesArr.push(res.data);
      }
    },
    //
    addExercise() {
      if (this.$route.params.enterType == 1) {
        Server.post(
          Path.addExercise,
          {
            courseid: this.courseId,
            periodid: this.periodId,
            subject: this.choiceQues.subject
          },
          res => {
            if (res.code === 0) {
              this.$router.push("/QuestionBank?from=TeachingAffairs");
              this.$Message.success(res.info);
            }
          }
        );
      } else if (this.$route.params.enterType == 2) {
        Server.post(
          Path.updateExercise,
          {
            id: this.$route.query.exeId,
            courseid: this.courseId,
            periodid: this.periodId,
            subject: this.choiceQues.subject
          },
          res => {
            if (res.code === 0) {
              this.$router.push("/QuestionBank?from=TeachingAffairs");
              // this.$router.push({path:'/QuestionBank',query:{from:'TeachingAffairs'}})
              this.$Message.success(res.info);
            } else {
              this.$Message.error(res.info);
            }
          }
        );
      }
    },
    //
    getExerciseinfo() {
      Server.post(
        Path.getExerciseinfo,
        {
          id: this.$route.query.exeId
        },
        res => {
          if (res.code === 0) {
            this.replaceBol = false;
            this.notSelect = true;
            this.choiceQues = res.data;
            this.courseId = res.data.courseid;
            this.getPeriodList(this.courseId);
            this.periodId = res.data.periodid;
            this.single = this.choiceQues.subject["1"];
            this.multiple = this.choiceQues.subject["2"];
            this.gapFilling = this.choiceQues.subject["3"];
            this.composition = this.choiceQues.subject["4"];
          }
        }
      );
    }
  },
  created() {
    this.getAllCurriculum();
    if (this.$route.params.enterType == 2) {
      this.pathArr = [
        {
          name: "",
          route: { path: "/QuestionBank", query: { from: "TeachingAffairs" } }
        },
        {
          name: "",
          route: {
            path: "/periodList",
            query: { from: "TeachingAffairs", id: this.$route.query.id }
          }
        },
        {
          name: "",
          route: ""
        }
      ];
      this.getExerciseinfo();
    } else {
      this.pathArr = [
        {
          name: "",
          route: { path: "/QuestionBank", query: { from: "TeachingAffairs" } }
        },
        {
          name: "",
          route: ""
        }
      ];
    }
  },
  mounted() {},
  computed: {},
  watch: {
    quesType(news) {
      if (this.$route.query.id && this.isHandle=='edit') {
        return;
      }
      this.$refs.topic.resetFields();
      this.topic = {
        stem: "", //
        imagesArr: [], //
        imgName: "", //
        optionA: "", //
        optionB: "",
        optionC: "",
        optionD: "",
        answer: "", //
        analytic: "", //
        score: "" //
      };
      this.multiSelect=[];
    }
  }
};
</script>
<style scoped lang='less'>
.head {
  display: flex;
  align-items: center;
  // padding-bottom:20px;
  // border-bottom: solid 1px #E6E6E6;
  img {
    width: 18px;
    height: 18px;
    margin-right: 20px;
  }
  span {
    font-size: 16px;
    font-family: MicrosoftYaHei-Bold;
    font-weight: bold;
    color: #666;
  }
}
.homework {
  width: 870px;
  height: auto;
  background: #fafbfc;
  border: 1px solid #cccccc;
  padding: 0 80px;
  // padding-right:200px;
  font-size: 14px;
  color: #7a8599;
  > div {
    padding: 15px 0;
  }
  dl {
    position: relative;
    text-indent: 1rem;
    border-bottom: solid 1px #eee;
    dt {
      line-height: 45px;
    }
    dd {
      text-indent: 2rem;
      line-height: 40px;
    }
  }
}
.delStyle {
  position: absolute;
  right: 0;
  top: 0;
  span {
    margin-right: 15px;
    color: #3f9ce8;
    cursor: pointer;
  }
  span:last-child {
    color: #ed3825;
  }
}
.radioGroup {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
//
.demo-upload-list {
  display: inline-block;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  margin-right: 20px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}
.demo-upload-list img {
  width: 100%;
  height: 100%;
}
.demo-upload-list-cover {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
}
.demo-upload-list:hover .demo-upload-list-cover {
  display: block;
}
.demo-upload-list-cover i {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin: 0 2px;
}
</style>



