<template>
  <div>
    <panel-title :crumbs="[{name:'',route:''}]" />
      <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
       <div class="mytheme-mg-top-20">
        <!---->
        <span class='fl-text'></span>
        <!-- @on-click="getCourseList(classtypes, current, keyWords)"-->
        <Input style="width:200px;" icon="ios-search"
               @on-enter="getCourseList(classtypes, current, keyWords)"
               v-model="courseKeyWord" placeholder=""/>
        <!---->
        <span class='fl-text'></span>
        <!-- @on-click="getCourseList(classtypes, current, keyWords)"-->
        <Input style="width:200px;" icon="ios-search"
               @on-enter="getCourseList(classtypes, current, keyWords)"
               v-model="classKeyWord" placeholder=""/>
        <!---->
        <span class='fl-text'></span>
        <!-- @on-click="getCourseList(classtypes, current, keyWords)"-->
        <Input style="width:200px;" icon="ios-search"
               @on-enter="getCourseList(classtypes, current, keyWords)"
               v-model="teacherKeyWord" placeholder=""/>
       </div>
      <!---->
      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></i-table>
      <!---->
      <pagination  class="mg-top-20">
          <!-- :total="total" :currentPage="current" :display="display" @pagechange="pageChange" v-if='total>0'-->
      </pagination>
    </Card>
     <!---->
     <Modal class='testModel'
            v-model="delModel"
            title="">
        <div class="type-content"></div>
         <div slot="footer">
          <!---->
          <Button type="text" shape="circle" @click="delModel = false;"></Button>
          <!-- @click="okDelete"-->
          <Button type="warning" shape="circle" @click="delModel = false;"></Button>
        </div>
  </Modal>
  </div>
</template>
<script>
import { Card, Input, Table, Modal,Button} from "iview";
import panelTitle from "@/components/panelTitle";
import pagination from '@/components/common/pagination'
export default {
  name: "SplendidMoment",
  components: {panelTitle,Card,Input,"i-table": Table,Modal,Button,pagination},
  data(){
    return {
      courseKeyWord:'',
      classKeyWord:'',
      teacherKeyWord:'',
     colData:[{
              title: '',
              key: 'number',
             align: 'center'
          },{
            title: '',
              key: 'courseName',
              align: 'center'
          },{
            title: '',
              key: 'className',
              align: 'center' 
          },{
            title: '',
              key: 'periodName',
              align: 'center' 
          },{
            title: '',
              key: 'teacher',
              align: 'center' 
          },{
            title: '',
              key: 'duration',
              align: 'center' 
          },{
            title: '',
              key: 'durarecordTimetion',
              align: 'center' 
          },{
            title: '',
              key: 'handle',
              align: 'center',
               render: (h, params, index) => {
            return h('div',[
              h('router-link',{
                props:{//'+params.row.id+'/'+params.row.curriculumid+'
                  to:'/splendidDetail/?from='+ this.$route.query.from
                },
                style:{
                  cursor:'pointer',
                  color: '#ffb932',
                  display: 'inline-block',
                  marginRight:'10px'
                },
                attrs:{
                  class: 'tablebtn yellow'
                }
              },''),//
               h('div',{
                on: {
                  'click': () => {
                    this.delModel = true;
                  }
                },
                  style:{
                    cursor:'pointer',
                    color: 'red',
                    display: 'inline-block'
                  },
                  attrs:{
                    class: 'tablebtn red'
                  }
                },'')//
            ])
          }
          }],
     tabData:[{
        number: 1,
        courseName: '',
        className:'1',
        periodName: '3',
        teacher:'',
        duration:'128',
        durarecordTimetion:'2018-06-20 18:00:03',
        handle: ''
     }],
     delModel:false//
    }
  },
  methods: {

  },
  created() {
  
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>
  @import "~@/assets/less/tab.less";

</style>



