<template>
  <div>
    <panel-title :crumbs="routeArr"/>
      <Tabs class='mytheme-tab' type="card" :animated="false" @on-click='msg' >
          <TabPane label="" name=''>
              <not-read></not-read>
          </TabPane>
          <TabPane label="" name=''>
            <have-read></have-read>
          </TabPane>
      </Tabs>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import {Tabs, TabPane} from "iview";
import haveRead from "./haveRead";
import notRead from "./notRead";
export default {
  name: "homework",
  components: {
    panelTitle,
    Tabs,
    TabPane,
    notRead,
    haveRead
  },
  data() {
    return {
      routeArr:[{name:'',route:''},{name:'',route:''}]
    };
  },
  methods: {
    msg(name){
     this.routeArr=[{name:'',route:''},{name:name,route:''}];
    }
  },
  created() {},
  mounted() {},
  computed: {},
  watch: {}
};
</script>
<style scoped lang='less'>
</style>



