import router from '@/router';
import sha1 from "sha1";
import md5 from "md5";

export default {
  getItem(param) {
    return localStorage.getItem(param);
  },

  jsonParse(jsonStr) {
    return JSON.parse(jsonStr);
  },

  /**
   * 
   * @param obj
   * @return {boolean}
   */
  isArray(obj) {
    return Object.prototype.toString.call(obj) == "[object Array]";
  },

  /**
   * Object
   * @param obj
   * @return {boolean}
   */
  isJson(obj) {
    return (
      typeof obj == "object" && Object.prototype.toString.call(obj).toLowerCase() == "[object object]" && !obj.length
    );
  },

  /**
   * 
   * @return {boolean}
   */
  isLogin() {
    if (!this.getItem('teacher_info') || !this.getItem('teacher_key')) {
      return false;
    } else {
      return true;
    }
  },

  /**
   * 
   * @param d2 
   * @return {*} true  false 
   */
  getNowTime(d2) {
    let date = new Date(),
      year = date.getFullYear(),
      month = date.getMonth() + 1,
      day = date.getDate(),
      hour = date.getHours(),
      minute = date.getMinutes();
    let dateStr = `${year}-${month}-${day} ${hour}:${minute}`;
    /**
     * 
     */
    if (d2) {
      return (
        new Date(dateStr.replace(/-/g, "/")) >= new Date(d2.replace(/-/g, "/"))
      );
    } else {
      return dateStr;
    }
  },

  /**
   * 
   * @return {*}
   */
  getUserInfo() {
    return this.jsonParse(this.getItem('teacher_info'));
  },

  /**
   *  URL 
   * @param query url 
   * @return Object  
   */
  parseQuery(query) {
    let sHref = query;
    let args = sHref.split('?');
    if (args[0] == sHref) {
      return "";
    }
    let arr = args[1].split('&');
    let obj = {};
    for (let i = 0; i < arr.length; i++) {
      let arg = arr[i].split('=');
      obj[arg[0]] = arg[1];
    }
    return obj;
  },

  /**
   * 
   * @param url 
   * @param key key
   * @returns {string}
   */
  getUrlMenuQuery(url, key) {
    let decoded = this.parseQuery(url)[key];
    if (decoded && decodeURIComponent(decoded).indexOf('&') != -1) {//
      decoded = this.parseQuery(decodeURIComponent(decoded))[key];
    }
    return decodeURIComponent(decoded);
  },

  /**
   * 
   * @param obj
   * @return {Array}
   */
  objectToArray(obj) {
    let keys = [], values = [], temp = [];
    keys = Object.keys(obj);
    values = Object.values(obj);
    keys.forEach((el, i) => {
      temp.push({
        keys: el,
        value: values[i],
      });
    })
    return temp;
  },
  /**
   * 
   * @return Object keyname---version
   */
  isBroswer() {
    let _broswer = {};
    let sUserAgent = navigator.userAgent;
    let isOpera = sUserAgent.indexOf("Opera") > -1;
    if (isOpera) {
      //Opera
      if (navigator.appName == 'Opera') {
        //
        _broswer.version = parseFloat(navigator.appVersion);
      } else {
        let reOperaVersion = new RegExp("Opera (\\d+.\\d+)");
        //testRegExp.$1
        reOperaVersion.test(sUserAgent);
        _broswer.version = parseFloat(RegExp['$1']);
      }
      _broswer.opera = true;
      _broswer.name = 'opera';
    }
    let isChrome = sUserAgent.indexOf("Chrome") > -1;
    if (isChrome) {
      let reChorme = new RegExp("Chrome/(\\d+\\.\\d+(?:\\.\\d+\\.\\d+))?");
      reChorme.test(sUserAgent);
      _broswer.version = parseFloat(RegExp['$1']);
      _broswer.chrome = true;
      _broswer.name = 'chrome';
    }
    //ChromeChromeuser-agentKonqueror/Safari
    let isKHTML = (sUserAgent.indexOf("KHTML") > -1
      || sUserAgent.indexOf("Konqueror") > -1 || sUserAgent
        .indexOf("AppleWebKit") > -1)
      && !isChrome;
    if (isKHTML) {//KHTMLKHTML
      let isSafari = sUserAgent.indexOf("AppleWebKit") > -1;
      let isKonq = sUserAgent.indexOf("Konqueror") > -1;
      if (isSafari) {
        let reAppleWebKit = new RegExp("Version/(\\d+(?:\\.\\d*)?)");
        reAppleWebKit.test(sUserAgent);
        let fAppleWebKitVersion = parseFloat(RegExp["$1"]);
        _broswer.version = parseFloat(RegExp['$1']);
        _broswer.safari = true;
        _broswer.name = 'safari';
      } else if (isKonq) {
        let reKong = new RegExp(
          "Konqueror/(\\d+(?:\\.\\d+(?\\.\\d)?)?)");
        reKong.test(sUserAgent);
        _broswer.version = parseFloat(RegExp['$1']);
        _broswer.konqueror = true;
        _broswer.name = 'konqueror';
      }
    }
    // !isOpera OperaIE
    let isIE = sUserAgent.indexOf("compatible") > -1
      && sUserAgent.indexOf("MSIE") > -1 && !isOpera;
    if (isIE) {
      let reIE = new RegExp("MSIE (\\d+\\.\\d+);");
      reIE.test(sUserAgent);
      _broswer.version = parseFloat(RegExp['$1']);
      _broswer.msie = true;
      _broswer.name = 'msie';
    }
    // Chrome  Konqueror/Safari 
    let isMoz = sUserAgent.indexOf("Gecko") > -1 && !isChrome && !isKHTML;
    if (isMoz) {
      let reMoz = new RegExp("rv:(\\d+\\.\\d+(?:\\.\\d+)?)");
      reMoz.test(sUserAgent);
      _broswer.version = parseFloat(RegExp['$1']);
      _broswer.mozilla = true;
      _broswer.name = 'mozilla';
    }
    return _broswer;
  },

  /**
   * 
   * @param date
   * @param format
   * @return {string}
   */
  formatDateStr(date, format) {
    let year = date.getFullYear(),
      month = (date.getMonth() + 1) >= 10 ? date.getMonth() + 1 : `0${date.getMonth() + 1}`,
      day = date.getDate() >= 10 ? date.getDate() : `0${date.getDate()}`,
      hour = date.getHours() >= 10 ? date.getHours() : `0${date.getHours()}`,
      minute = date.getMinutes() >= 10 ? date.getMinutes() : `0${date.getMinutes()}`,
      second = date.getSeconds() >= 10 ? date.getSeconds() : `0${date.getSeconds()}`,
      timeStamp = date.getTime() / 1000;
    let formatStr = '';
    switch (format) {
      case 'yyyy-MM-dd':
        formatStr = `${year}-${month}-${day}`;
        break;
      case 'yyyy-MM-dd HH:mm:ss':
        formatStr = `${year}-${month}-${day} ${hour}:${minute}:${second}`;
        break;
      case 'yyyy':
        formatStr = `${year}`;
        break;
      case 'yyyy-MM':
        formatStr = `${year}-${month}`;
        break;
      case 'HH:mm:ss':
        formatStr = `${hour}:${minute}:${second}`;
        break;
      case 'HH:mm':
        formatStr = `${hour}:${minute}`;
        break;
      case 'timestamp':
        formatStr = `${timeStamp}`;
        break;
      default: formatStr = `${year}-${month}-${day}`;
    }
    return formatStr;
  },

  formatStampStr(timestamp, format) {
    let date = new Date();
    date.setTime(timestamp * 1000);
    let year = date.getFullYear(),
      month = (date.getMonth() + 1) >= 10 ? date.getMonth() + 1 : `0${date.getMonth() + 1}`,
      day = date.getDate() >= 10 ? date.getDate() : `0${date.getDate()}`,
      hour = date.getHours() >= 10 ? date.getHours() : `0${date.getHours()}`,
      minute = date.getMinutes() >= 10 ? date.getMinutes() : `0${date.getMinutes()}`,
      second = date.getSeconds() >= 10 ? date.getSeconds() : `0${date.getSeconds()}`;
    let formatStr = '';
    switch (format) {
      case 'yyyy-MM-dd':
        formatStr = `${year}-${month}-${day}`;
        break;
      case 'yyyy-MM-dd HH:mm:ss':
        formatStr = `${year}-${month}-${day} ${hour}:${minute}:${second}`;
        break;
      case 'yyyy':
        formatStr = `${year}`;
        break;
      case 'yyyy-MM':
        formatStr = `${year}-${month}`;
        break;
      case 'timestamp':
        formatStr = `${timeStamp}`;
        break;
      default: formatStr = `${year}-${month}-${day}`;
    }
    return formatStr;
  },

  getStr(arr, key, starttime, token) {
    var tostr = arr ? this.getStrings(arr, "") : "";
    return md5(sha1(md5(tostr) + md5(key) + starttime) + token);
  },
  getStrings(data, str) {
    for (var x in data) {
      if (this.isJson(data[x])) {
        var i = 0;
        for (var k in data[x]) {
          i++;
          break;
        }
        if (i > 0) {
          str += x;
          str = this.getStrings(data[x], str);
        }
      } else if (this.isArray(data[x])) {
        if (data[x].length != 0) {
          str += x;
          str = this.getStrings(data[x], str);
        }
      } else {
        if (
          data[x] !== undefined &&
          data[x] !== null &&
          data[x] !== "" &&
          data[x] !== false &&
          data[x] !== true
        ) {
          str += x.toString() + data[x].toString();
        }
      }
    }
    return str;
  },

  setHeader(param) {
    var token = JSON.parse(window.sessionStorage.getItem("userinfo")).token;
    var starttime = Date.parse(new Date());
    var head = {
      sign: this.getStr(
        param,
        window.sessionStorage.getItem("key"),
        starttime,
        token
      ),
      token: token,
      starttime: starttime
    };
    return head;
  },
}




