											<Button class="classBtn" :class= "{btnActive : allClass1}" @click="active1 ('allClass1',0)"></Button>
											<Button class="classBtn" :class= "{btnActive : Appoint1}"  @click="active1 ('Appoint1',1)"></Button>
											<Button class="classBtn" :class= "{btnActive : fication1}" @click="active1 ('fication1',2)"></Button>
		              </FormItem>
