		              			<span v-if="activePlus==2" class="classification edit" @click = "isShowDelModall = true ">{{classifyNum1}}</span>
		              			<span v-if="activePlus==3" class="classification edit" @click = "isShowDell = true ">{{stragePlus.length}}</span>
