<template>
  <div id="order-details">
    <!-- <panel-title> > </panel-title> -->
    <!--  -->
    <panel-title
      :crumbs="[
        {
          name:$t('orderList.orderList'),//
          route:{
            path:'/AccountDetails',
            query:{
              from:'MainOrderManagement',
            }
          }
        },
        {
          name:$t('orderDetails.orderDetails'),//
          route:'',
        },
      ]"
    ></panel-title>
    <template>
      <Card dis-hover class="mytheme-pd-left-20 mytheme-pd-right-20 mytheme-mg-bottom-30">
        <!--  -->
        <div slot="title" class="head">
          <img :src="orderForm">
          <span>{{$t('orderDetails.orderinfo')}}</span>
        </div>
        <!-- <p></p> -->
        <!---->
        <div class="callout callout-info" v-if="isHasOrderInfo">
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('orderManagepage.reference')}}{{$t('Common.colon')}}</label>
            <span >{{buyer.ordernum}}</span>
          </div>
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('orderManagepage.orderstatus')}}{{$t('Common.colon')}}</label>
            <span >{{buyer.orderstatus}}</span>
          </div>
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('orderManagepage.ordertime')}}{{$t('Common.colon')}}</label>
            <span >{{buyer.ordertime}}</span>
          </div>
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('orderManagepage.student')}}{{$t('Common.colon')}}</label>
            <span >{{buyer.studentname}}</span>
          </div>
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('orderDetails.webtype')}}{{$t('Common.colon')}}</label>
            <!-- 1pc 2 -->
            <span>{{buyer.ordersource}}</span>
  
          </div>
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('orderList.payway')}}{{$t('Common.colon')}}</label>
            <span >{{buyer.paytype}}</span>
          </div>
        </div>
        <!---->
        <div class="callout callout-warning" v-if="isHasOrderInfo">
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('orderDetails.originprice')}}{{$t('Common.colon')}}</label>
            <span >{{seller.originprice}}</span>
          </div>
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('orderDetails.discount')}}{{$t('Common.colon')}}</label>
            <span >{{seller.discount}}</span>
          </div>
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('orderDetails.amount')}}{{$t('Common.colon')}}</label>
            <span>{{seller.amount}}</span>
          </div>
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('courseManagepage.coursetitle')}}{{$t('Common.colon')}}</label>
            <span >{{seller.coursename}}</span>
          </div>
          <!--  -->
          <!-- <div class="callout-item">
            <label style="">{{$t('arrangManage.classname')}}{{$t('Common.colon')}}</label>
            <span >{{seller.classname}}</span>
          </div> -->
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('orderManagepage.lecturer')}}{{$t('Common.colon')}}</label>
            <span >{{seller.teachername}}</span>
          </div>
          <div class="callout-item">
            <!--  -->
            <label style="">{{$t('Common.colon')}}</label>
            <span >{{seller.address}}</span>
          </div>
        </div>
        <!---->
        <div v-if="!isHasOrderInfo">{{message}}</div>
      </Card>
    </template>
  </div>
</template>

<script>
  import {Card, Button, Form, FormItem, Col, Row} from 'iview'
  import panelTitle from '@/components/panelTitle'
  import orderForm from '../img/orderForm@2x.png'
  import callout from '@/components/callout'
  import Server from '@/service/request'
  import Path from '@/service/Path'

  export default {
    name: "order-details",
    components: {
      Card,
      Button,
      Form,
      FormItem,
      panelTitle,
      callout,
      Col,
      Row
    },
    created() {
      this.getOrderInfo();
    },
    data() {
      return {
        num:'',
        orderForm,
        buyer: {
          ordernum: '', //
          orderstatus: '', //
          ordertime: '', // 
          studentname: '', //
          ordersource: '', //
          paytype: '', //
        },
        seller: {
          originprice: '', //
          discount: '', //
          amount: '', //
          coursename: '', //
          classname: '', //
          teachername: '', // 
          address:'',//
        },
        refund: {},
        isFinishe: false,
        isRefund: false,
        isHasOrderInfo: false,
        message: ''
      }
    },
    methods: {
      getOrderInfo() {
        let  id  = this.$route.query.id;
        let  num  = this.$route.query.num;
        Server.post(Path.addWithdraw, {
            id: id
        }, (res) => {
          console.log(res)
          let { code, data } = res;
          if(res.code === 0) {
            // 
            this.buyer.ordernum = num;//
            this.buyer.ordertime = res.data.paytime;//
            this.buyer.paytype = res.data.paytype;//
            this.buyer.ordersource = res.data.ordersource;//
            this.buyer.studentname = res.data.studentname;//
            this.buyer.orderstatus = res.data.orderstatus;//
            // 
            this.seller.originprice = res.data.originprice;//
            this.seller.discount = res.data.discount;//
            this.seller.amount = res.data.amount;//
            this.seller.coursename = res.data.coursename;//
            this.seller.classname = res.data.classname;//
            this.seller.teachername = res.data.teachername;//
            this.seller.address = res.data.address;//
            this.isHasOrderInfo = true;
          } else {
            this.isHasOrderInfo = false;
            this.message = res.info;
          }
        });
      },
      //
      // paytype(val) {
      //   if(val==1){
      //     return this.$t('orderList.paytype2')//
      //   }else if(val==2){
      //     return this.$t('orderList.paytype3')//
      //   }else if(val==3){
      //     return this.$t('orderList.paytype4')//
      //   }else if(val==4){
      //     return this.$t('orderList.paytype5')//
      //   }else if(val==5){
      //     return 'paypal' //paypal
      //   }else{
      //     return this.$t('orderList.paytype1')//
      //   }
      // },
      //
      orderstatus(val){
        if(val==10){
          return this.$t('orderList.status3')//
        }else if(val==20){
          return this.$t('orderList.status4')//
        }else if(val==0){
          return this.$t('orderList.status5')//
        }else if(val==30){
          return this.$t('orderList.status6')//
        }else if(val==40){
          return this.$t('orderList.status7')//
        }else if(val==50){
          return this.$t('orderList.status8')//
        }
      }
    }
  }
</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
 .head{
    display: flex;
    align-items: center;
    img{
      width: 16px;
      height: 20px;
      margin-right: 20px;
    }
    span{
      font-size:16px;
      font-family:MicrosoftYaHei-Bold;
      font-weight:bold;
      color:#666;
    }
  }
.ivu-card-head{
  background: @bg1;
}
  .callout {
    padding: 30px 0 30px 60px;
    margin: 20px 40px 30px;
    border: 1px solid #eee;
    border-left-width: 5px;
    border-radius: 4px;
  }
  .callout-item {
    color: @fz-Color1;
    line-height: 32px;
  }
  .callout-item label {
    display: inline-block;
    text-align: right;
    width: 100px;
    font-size: 16px;
  }
  .callout-item span {
    padding: 0 0 0 50px;
    font-size: 16px;
  }

  .callout,
  .callout-default {
    border-left-color: #999;
    background: #eee;
  }

  .callout-info {
    border-left-color: #469af8;
    background: #f2f7fb;
  }

  .callout-warning {
    border-left-color: #f89240;
    background: #fffbf8;
  }

  .callout-danger {
    border-left-color: #f5496d;
    background: #f2dbe0;
  }

  .callout-primary {
    border-left-color: #cae3ff;
    background: #fff;
  }

  .callout-success {
    border-left-color: #3adfa7;
    background: #f7fffc;
  }
</style>



