<template>
  <div>
    <panel-title :crumbs="[{name:'',route:''}]" />
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
       <div class="mytheme-mg-top-20">
          <!---->
      <span></span>
      <!--,fromPage: 'add'-->
      <router-link :to="{ path: '/RewardShowAdd/add/1', query: { from:'TeachingAffairs'}}">
        <Button type="primary" class='fr'></Button>
      </router-link>
        </div>
        <!---->
        <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></i-table>
        <!---->
      <pagination  class="mg-top-20" :total="total" :currentPage="current" :display="display" @pagechange="pagechange" v-if='total>0'></pagination>
    </Card>
    <!---->
    <Modal
      v-model="deleteItemModal"
      title="" >
      <p>
        
      </p>
      <div slot="footer">
        <!---->
        <Button type="text" shape="circle" @click="deleteItemModal = false;">{{$t('Common.cancel')}}</Button>
        <!---->
        <Button type="warning" shape="circle" @click="okDelete">{{$t('Common.sure')}}</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
// import JsonInfo from '@/assets/testInfo/RewardSet';
import Server from "@/service/request";
import Path from "@/service/Path";
import { Card,  Table, Button,Modal,Form,Row,Col} from "iview";
export default {
  name: "",
  components: {panelTitle,pagination,Card,'i-table':Table, Button,Modal,Form,Row,Col},
   data(){
    return {
      total: 0, // 
      display: 10, // 
      current: 1, // 
      deleteItemModal:false,   //
      deleteId:null,   //ID
      colData: [
        {
          title: '',
          key: 'id',
          align: "center",
          width:120
        },
        {
          title: '',
          key: 'name',
          align: "center",
          width:200
        },
        {
          title: '',
          align: "center",
          key: 'type',
          render: (h, params) => {
            let typeText ='';
            if(params.row.type==1){
                typeText='';
            }else if(params.row.type==2){
                typeText='';
            }
            return h("div",{},typeText);
          },
        },
        {
          title: '',
          align: "center",
          width:250,
          key: 'condition',
          render: (h, params) => {
            return h("div",{},
            ''+params.row.condition1+''+params.row.condition2
            );
          },
        },
        {
          title: '',
          key: 'status',
          align: "center",
          render: (h, params) => {
            return h('i-switch', {
              on: {
                'on-change': (value) => {
                     Server.post(Path.SwitchRewardStatus,{
                            id:params.row.id,
                            status:Number(value)
                        },res=>{
                          let { code, data } = res;
                          if(code===0){
                            this.$Message.info(res.info);
                            this.getTableInfo(this.current);
                          }else{
                            this.$Message.error(res.info);
                          }
                        })
                }
              },
              props: {
                // value: params.row.status === 0
                value: Boolean(params.row.status),
              }
            })
          }
        },
        {
          title: '',
          key: 'addtime',
          align: "center"
        },
        {
          title: '',
          render: (h, params) => {
            return h("div", [
              h("a",{
                style: {
                  color: "#f89240",
                  display: "inline-block",
                  marginRight: "10px"
                },
                on: {
                  'click': () => {
                    this.$router.push({
                      path: '/RewardShowAdd/show/'+params.row.id,
                      query: {
                        from:"TeachingAffairs"
                      }
                    })
                  }
                }
              },""), //
              h("a",{
                style: { color: "#f5496d", display: "inline-block"},
                on: {
                  'click': () => {
                  // ID
                    this.deleteId = params.row.id;
                    // 
                    this.deleteItemModal = true;
                  }
                }
              },"") //
            ]);
          },
          align: "center"
        }

      ],
      tabData: [
      ]
    }
  },
  methods: {
    //
    getTableInfo(current=1) {
      Server.post(Path.getRewardList,{
          pagenum: current,
          name: '',
        },
        res => {
          if (res.code === 0) {
            this.tabData = res.data.data;
            this.total = res.data.pageinfo.total;
            this.display = res.data.pageinfo.pagesize;
            this.current = Number(res.data.pageinfo.pagenum);
          }
        }
      );
    },
    //
    pagechange: function(currentPage) {
      this.getTableInfo(this.current);
    },
    okDelete(){//
    Server.post(Path.deleteReward,{
          id:this.deleteId
        },
        res => {
          if (res.code === 0) {
           this.$Message.success(res.info);
           this.getTableInfo(this.current);
          }
        }
      );
      this.deleteItemModal = false;
    },
  },
  created() {
    this.getTableInfo(this.current);
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>
  @import "~@/assets/less/tab.less";
</style>



