<template>
  <div id="Course">
     <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="mytheme-mg-top-20">
          <!---->
          <span>{{$t('Common.colon')}}</span>
          <!---->
          <Input
            v-model.trim="InputText"
            placeholder=""
            @on-enter="getStuList(InputText, nickname, current)"
           class="input-normal-width input-max-height"/>
          <!---->
          <span class="mytheme-mg-left-70">{{$t('Common.colon')}}</span>
          <!---->
          <Input
            v-model.trim="nickname"
            placeholder=""
            @on-enter="getStuList(InputText, nickname, current)"
           class="input-normal-width input-max-height"/>

          <Button type="primary" class="mytheme-mg-left-70" @click="getStuList(InputText, nickname, current)"></Button>
      </div>

      <!--  -->
      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe ref="selection" :loading="loading" :columns="columns1" :data="data1"></i-table>
      
       <!--  -->
      <pagination :total="total" :display="display" class="mg-top-20" :current-page='current' @pagechange="pagechange" v-if='total>0'></pagination>
   </Card>

  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";
import {
  Card,
  Input,
  Table,
  Switch,
  Button,
  Form,
  Row,
  Col,
  FormItem,
  Checkbox,
  Modal
} from "iview";
export default {
  name: "",
  components: {
    panelTitle,
    pagination,
    Card,
    Input,
    "i-table": Table,
    "i-switch": Switch,
    Button,
    Form,
    Row,
    Col,
    FormItem,
    Checkbox,
    Modal
  },
  created() {
    this.getStuList(this.mobile, this.nickname, this.current);
  },
  watch: {
  },
  methods: {
    //
    getStuList(mobile, nickname, page = 1) {
      Server.post(
        Path.getHjxUserList,
        {
          mobile: mobile,
          nickname: nickname,
          pagenum: page
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            let { pagenum, pagesize, total } = data.pageinfo;
            this.total = total;
            this.display = pagesize;
            this.current = Number(pagenum);
            this.data1 = data.data;
            this.loading = false;
          } else {
            this.data1 = [];
            this.loading = false;
          }
        }
      );
    },
    //
    pagechange(currentPage) {
      this.getStuList(this.mobile, this.nickname, currentPage);
    },
  },
  data() {
    return {
      // mobile: "",
      nickname: "",//
      InputText: "",//
      loading: true,//
      total: 0, // 
      display: 20, // 
      current: 1, // 
      studentids:[],//ID
      columns1: [
        {
          title: "ID", //ID
          align: "center",
          key: "id"
        },
        {
          title: "", //
          key: "mobile",
          align: "center"
        },
        {
          title: "", //
          align: "center",
          key: "nickname"
        },
        {
          title: this.$t("studentManagepage.sex"), //
          align: "center",
          key: "sex",
          render: (h, params, index) => {
            let Sex = params.row.sex;
            switch (Sex) {
              case 0:
                Sex = this.$t("teacherDetails.unknow"); //
                break;
              case 1:
                Sex = this.$t("teacherDetails.male"); //
                break;
              case 2:
                Sex = this.$t("teacherDetails.female"); //
                break;
            }
            return h("div", {}, Sex);
          }
        },
        {
          title: "", //
          align: "center",
          key: "categoryname",
          render: (h, params) => {
            return h("div", params.row.school || "---");
          }
        },
        {
          title: "", //
          align: "center",
          key: "categoryname",
          render: (h, params) => {
            return h("div", params.row.grade || "---");
          }
        },
        {
          title: "", //
          align: "center",
          key: "categoryname",
          render: (h, params) => {
            return h("div", params.row.class || "---");
          }
        },
        {
          title: "", 
          key: "logintime",
          align: "center"
        },
        {
          title: "",
          align: "center",
          key: "status",
          render: (h, params, index) => {
            return h("div", [
              h("i-switch", {
                on: {
                  "on-change": value => {
                    Server.post(
                      Path.changeHjxUserStatus,
                      {
                        userid: params.row.id,
                        flag: !Number(value)
                      },
                      res => {
                        let { code, data } = res;
                        if (code === 0) {
                          this.$Message.info(res.info);
                        } else {
                          this.$Message.error(res.info);
                        }
                      }
                    );
                  }
                },
                props: {//01
                  value: !Boolean(params.row.status)
                }
              })
            ]);
          }
        }
      ],
      data1: []
    };
  }
};
</script>
<style scoped lang='less'>
</style>



