<template>
  <div id="money-center" style="min-height:500px;">
    <!---->
    <panel-title :crumbs="[
      {
        name:$t('withdrawalcheckCenterpage.withdrawalcheckCenterpage'),
        route:''
      },
    ]" />
	    	<div>
		      <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
		        <div class="mytheme-mg-top-20">
			            <!---->
			            <span ></span>
			            <!---->
			            <Input class="input-normal-width input-max-height" placeholder=""  @on-enter="getOrderList(current,serchaName,keName,className)"  v-model="serchaName"/>
			            
			            <!---->
			            <span class="mytheme-mg-left-70"></span>
			            <!---->
			            <Input class="input-normal-width input-max-height" placeholder="" @on-enter="getOrderList(current,serchaName,keName,className)" v-model="keName" />
			
			            <!---->
			            <span class="mytheme-mg-left-70"></span>
			            <!---->
			            <Input class="input-normal-width input-max-height" placeholder="" v-model="className" @on-enter="getOrderList(current,serchaName,keName,className)"/>
				          <Button type="primary" class="mytheme-mg-left-70" @click="getOrderList(current,serchaName,keName,className)">{{$t('withdrawalcheckCenterpage.search')}}</Button>
		        </div>
		       	<Table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :loading="loading" :columns="colData" :display="display" :data="tabData"></Table>
				    <pagination class="mg-top-20" :total="total" :display="display" :current-page="current" @pagechange="pageChange" v-if='total>0'></pagination>
		     </Card>
	     </div>
  </div>
</template>

<script>
import {
  Table,
  Button,
  ButtonGroup,
  Card,
  Input,
  Icon,
  DatePicker
} from "iview";
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";

export default {
  name: "money-center",
  components: {
    Table,
    Button,
    ButtonGroup,
    Card,
    panelTitle,
    Input,
    pagination,
    Icon,
    Server,
    Path,
    DatePicker
  },
  data() {
    return {
      colData: [
        { title: 'ID', key: "teacherid", align: "center" }, //ID
        { title: '', key: "teachername", align: "center" }, //
        { title: '', key: "curriculumname", align: "center" }, //
        { title: '', key: "gradename", align: "center" }, //
        { title: '/', key: "num", align: "center" }, //
        {
          title: "",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h('div',[
              h('router-link',{
                props:{
                    to:`/TeacherMoney?id=${this.tabData[params.index].id}&curr=${params.row.curriculumname}&class=${params.row.gradename}&name=${params.row.teachername}&from=MainOrderManagement`
                },
                style: {
                  cursor:'pointer',
                  color: '#3F9CE8',
                  display: 'inline-block'
                },
                attrs:{
                  class: 'tablebtn blue',
                }
              },'')
            ])
          }
        }
      ],
      tabData: [],
      current: 1,
      total: 0,
      display: 20,
      loading: true,
      classId: 0,
      className:'',//
      keName:'',//
      serchaName:''//
    };
  },
  created() {
    this.getOrderList(this.current,this.serchaName,this.keName,this.className);
  },
  methods: {
    // 
    getOrderList(current = 1,teacher,curr,grade) {
      Server.post( Path.getBill,{
          'pagenum':current,
					'teachername':teacher,//
					'curriculumname':curr,//
					'gradename':grade//
        },
        res => {
        	this.tabData = [];
          let { code, data } = res;
          if (code === 0) {
          	let datas =data.data;
						for (let i = 0; i < datas.length; i++ ) {
							let obj = {};
							obj.id = datas[i].id;
							obj.teacherid = datas[i].teacherid;
							obj.teachername = datas[i].teachername;
							obj.curriculumname = datas[i].curriculumname;
							obj.gradename = datas[i].gradename;
							obj.periodnum = datas[i].periodnum;
							obj.completenum = datas[i].completenum;
							obj.num = datas[i].completenum+'/'+datas[i].periodnum;
							this.tabData.push(obj);
						}
						this.total = data.pageinfo.total;
			      this.display = data.pageinfo.pagesize;
          }else{
            // this.$Message.error(res.info);
          }
          this.loading = false;
        }
      );
    },
    // 
    pageChange(currentPage) {
    	this.current = currentPage;
      this.loading = true;
      this.getOrderList(currentPage,this.serchaName,this.keName,this.className);
    }
  },
  computed: {}
};
</script>

<style scoped lang='less'>
.date-change {
  overflow: hidden;
  padding: 0 0 20px 0;
}
.date-change li {
  cursor: pointer;
  margin-right: 20px;
}
.br8{
  overflow: initial;
}
</style>



